"use client";

import { forwardRef } from "react";
import { Dialog as DrawerPrimitive } from "@lion-libs/react-ui-base";
import { cn } from "../../helper";

type DrawerRootProps = React.ComponentPropsWithoutRef<
  typeof DrawerPrimitive.Root
>;
function DrawerRoot(props: DrawerRootProps) {
  return <DrawerPrimitive.Root {...props} />;
}

type DrawerTriggerElement = React.ElementRef<typeof DrawerPrimitive.Trigger>;
type DrawerTriggerProps = React.ComponentPropsWithoutRef<
  typeof DrawerPrimitive.Trigger
>;
const DrawerTrigger = forwardRef<DrawerTriggerElement, DrawerTriggerProps>(
  ({ ...props }, ref) => <DrawerPrimitive.Trigger {...props} ref={ref} />
);

type DrawerPortalProps = React.ComponentPropsWithoutRef<
  typeof DrawerPrimitive.Portal
>;
function DrawerPortal(props: DrawerPortalProps) {
  return <DrawerPrimitive.Portal {...props} />;
}

type Color = "transparent" | "black" | "slate";
type DrawerOverlayElement = React.ElementRef<typeof DrawerPrimitive.Overlay>;
type DrawerOverlayProps = React.ComponentPropsWithoutRef<
  typeof DrawerPrimitive.Overlay
> & {
  color?: Color | "black";
};

const drawerOverlayBaseStyles = "fixed inset-0";
const drawerOverlayColorStyles = {
  transparent: "bg-transparent",
  black: "bg-black-500/[0.2]",
  slate: "bg-slate-500",
};
const DrawerOverlay = forwardRef<DrawerOverlayElement, DrawerOverlayProps>(
  ({ className, color = "black", ...props }, ref) => {
    const drawerOverlayStyles = `${drawerOverlayBaseStyles} ${drawerOverlayColorStyles[color]}`;
    return (
      <DrawerPrimitive.Overlay
        className={cn(drawerOverlayStyles, className)}
        {...props}
        ref={ref}
      />
    );
  }
);

type DrawerContentElement = React.ElementRef<typeof DrawerPrimitive.Content>;
type DrawerContentProps = React.ComponentPropsWithoutRef<
  typeof DrawerPrimitive.Content
> & {
  open?: boolean;
  slideDirection?: "left" | "right" | "top" | "bottom"; // 動畫方嚮
  slideWidth?: "full" | "half" | "three4th";
  duration?: "100" | "200" | "300" | "400" | "500" | "600" | "700" | "800" | "900" | "1000";
};

const DrawerContent = forwardRef<DrawerContentElement, DrawerContentProps>(
  (
    {
      className,
      open,
      slideDirection = "right",
      slideWidth = "full",
      duration = "300",
      ...props
    },
    ref
  ) => {

    let animationStyle = ''
    // 根據方向和開關狀態選擇動畫
    if (duration === '100') {
      animationStyle = {
        right: open ? `animate-slideInRight100` : `animate-slideOutRight100`,
        left: open ? `animate-slideInLeft100` : `animate-slideOutLeft100`,
        top: open ? `animate-slideInTop100` : `animate-slideOutTop100`,
        bottom: open ? `animate-slideInBottom100` : `animate-slideOut-bottom100`,
      }[slideDirection];
    } else if
      (duration === '200') {
      animationStyle = {
        right: open ? `animate-slideInRight200` : `animate-slideOutRight200`,
        left: open ? `animate-slideInLeft200` : `animate-slideOutLeft200`,
        top: open ? `animate-slideInTop200` : `animate-slideOutTop200`,
        bottom: open ? `animate-slideInBottom200` : `animate-slideOut-bottom200`,
      }[slideDirection];
    } else if
      (duration === '300') {
      animationStyle = {
        right: open ? `animate-slideInRight300` : `animate-slideOutRight300`,
        left: open ? `animate-slideInLeft300` : `animate-slideOutLeft300`,
        top: open ? `animate-slideInTop300` : `animate-slideOutTop300`,
        bottom: open ? `animate-slideInBottom300` : `animate-slideOut-bottom300`,
      }[slideDirection];
    } else if
      (duration === '400') {
      animationStyle = {
        right: open ? `animate-slideInRight400` : `animate-slideOutRight400`,
        left: open ? `animate-slideInLeft400` : `animate-slideOutLeft400`,
        top: open ? `animate-slideInTop400` : `animate-slideOutTop400`,
        bottom: open ? `animate-slideInBottom400` : `animate-slideOut-bottom400`,
      }[slideDirection];
    } else if
      (duration === '500') {
      animationStyle = {
        right: open ? `animate-slideInRight500` : `animate-slideOutRight500`,
        left: open ? `animate-slideInLeft500` : `animate-slideOutLeft500`,
        top: open ? `animate-slideInTop500` : `animate-slideOutTop500`,
        bottom: open ? `animate-slideInBottom500` : `animate-slideOut-bottom500`,
      }[slideDirection];
    } else if
      (duration === '600') {
      animationStyle = {
        right: open ? `animate-slideInRight600` : `animate-slideOutRight600`,
        left: open ? `animate-slideInLeft600` : `animate-slideOutLeft600`,
        top: open ? `animate-slideInTop600` : `animate-slideOutTop600`,
        bottom: open ? `animate-slideInBottom600` : `animate-slideOut-bottom600`,
      }[slideDirection];
    } else if
      (duration === '700') {
      animationStyle = {
        right: open ? `animate-slideInRight700` : `animate-slideOutRight700`,
        left: open ? `animate-slideInLeft700` : `animate-slideOutLeft700`,
        top: open ? `animate-slideInTop700` : `animate-slideOutTop700`,
        bottom: open ? `animate-slideInBottom700` : `animate-slideOut-bottom700`,
      }[slideDirection];
    } else if
      (duration === '800') {
      animationStyle = {
        right: open ? `animate-slideInRight800` : `animate-slideOutRight800`,
        left: open ? `animate-slideInLeft800` : `animate-slideOutLeft800`,
        top: open ? `animate-slideInTop800` : `animate-slideOutTop800`,
        bottom: open ? `animate-slideInBottom800` : `animate-slideOut-bottom800`,
      }[slideDirection];
    } else if
      (duration === '900') {
      animationStyle = {
        right: open ? `animate-slideInRight900` : `animate-slideOutRight900`,
        left: open ? `animate-slideInLeft900` : `animate-slideOutLeft900`,
        top: open ? `animate-slideInTop900` : `animate-slideOutTop900`,
        bottom: open ? `animate-slideInBottom900` : `animate-slideOut-bottom900`,
      }[slideDirection];
    } else if
      (duration === '1000') {
      animationStyle = {
        right: open ? `animate-slideInRight1000` : `animate-slideOutRight1000`,
        left: open ? `animate-slideInLeft1000` : `animate-slideOutLeft1000`,
        top: open ? `animate-slideInTop1000` : `animate-slideOutTop1000`,
        bottom: open ? `animate-slideInBottom1000` : `animate-slideOut-bottom1000`,
      }[slideDirection];
    }
    const widthClass = (() => {
      switch (slideDirection) {
        case "right":
          return {
            full: "top-0 w-full right-0",
            half: "top-0 w-1/2 right-0",
            three4th: "top-0 w-3/4 right-0",
          };
        case "left":
          return {
            full: "top-0 w-full left-0",
            half: "top-0 w-1/2 left-0",
            three4th: "top-0 w-3/4 left-0",
          };
        case "top":
          return {
            full: "top-0 h-full left-0",
            half: "top-0 h-1/2 left-0",
            three4th: "top-0 h-3/4 left-0",
          };
        case "bottom":
        default:
          return {
            full: "bottom-0 h-full left-0",
            half: "bottom-0 h-1/2 left-0",
            three4th: "bottom-0 h-3/4 left-0",
          };
      }
    })();

    const DrawerContentStyle = cn(
      "bg-white fixed h-full w-full p-5 z-50",
      {
        [animationStyle]: true,
      },
      widthClass[slideWidth],
      className
    );

    return (
      <DrawerPrimitive.Content
        className={DrawerContentStyle}
        {...props}
        ref={ref}
      />
    );
  }
);

type DrawerCloseElement = React.ElementRef<typeof DrawerPrimitive.Close>;
type DrawerCloseProps = React.ComponentPropsWithoutRef<
  typeof DrawerPrimitive.Close
>;
const DrawerClose = forwardRef<DrawerCloseElement, DrawerCloseProps>(
  ({ ...props }, ref) => <DrawerPrimitive.Close {...props} ref={ref} />
);

type DrawerTitleElement = React.ElementRef<typeof DrawerPrimitive.Title>;
type DrawerTitleProps = React.ComponentPropsWithoutRef<
  typeof DrawerPrimitive.Title
>;
const DrawerTitle = forwardRef<DrawerTitleElement, DrawerTitleProps>(
  ({ ...props }, ref) => <DrawerPrimitive.Title {...props} ref={ref} />
);

type DrawerDescriptionElement = React.ElementRef<
  typeof DrawerPrimitive.Description
>;
type DrawerDescriptionProps = React.ComponentPropsWithoutRef<
  typeof DrawerPrimitive.Description
>;
const DrawerDescription = forwardRef<
  DrawerDescriptionElement,
  DrawerDescriptionProps
>(({ ...props }, ref) => <DrawerPrimitive.Description {...props} ref={ref} />);

const Drawer = {
  Root: DrawerRoot,
  Trigger: DrawerTrigger,
  Portal: DrawerPortal,
  Overlay: DrawerOverlay,
  Content: DrawerContent,
  Close: DrawerClose,
  Title: DrawerTitle,
  Description: DrawerDescription,
};

export {
  Drawer,
  DrawerRoot,
  DrawerTrigger,
  DrawerPortal,
  DrawerOverlay,
  DrawerContent,
  DrawerClose,
  DrawerTitle,
  DrawerDescription,
};
