
import { slideAnimations, Keyframes, Animations } from './drawer';

type AnimationSetting = { keyframes: Keyframes; animation: Animations };

const customKeyframes: Keyframes = {
  dotFade: {
    "0%, 39%, 100%": { opacity: '0' },
    "40%": { opacity: '1' },
  },
  spin: {
    "100%": { transform: "rotate(360deg)" },
  },
  pulse: {
    "100%": { transform: "translateX(100%)" },
  },
  in: {
    "0%": { opacity: '0' },
    "100%": { opacity: '1' },
  },
  out: {
    "0%": { opacity: '1' },
    "100%": { opacity: '0' },
  },
};

const customAnimations: Animations = {
  dotFade: "dotFade 1.2s ease-in-out 0s infinite normal both running",
  spin: "spin 1.2s linear 0s infinite",
  pulse: "pulse 2s ease 0s infinite normal none running",
  in: "in 500ms linear 0s",
  out: "out 100ms ease-in",
};

const combinedKeyframes: Keyframes = { ...customKeyframes };
const combinedAnimations: Animations = { ...customAnimations };

// 合並所有的keyframes和animations
Object.values(slideAnimations as { [key: string]: AnimationSetting }).forEach(animationSet => {
  Object.assign(combinedKeyframes, animationSet.keyframes);
  Object.assign(combinedAnimations, animationSet.animation);
});

export default {
  keyframes: combinedKeyframes,
  animation: combinedAnimations,
};