import { forwardRef, ElementRef, ComponentPropsWithoutRef } from "react";
import { cn } from "../../helper";

type DivElement = ElementRef<"div">;
type DivProps = ComponentPropsWithoutRef<"div"> & {
  className?: string;
};

const skeletonBaseStyle = `relative bg-neutral-400
  before:content-['']  before:absolute before:w-full before:h-full before:translate-x-[-100%]
  before:bg-gradient-to-r from-transparent via-white-500 to-transparent before:rounded 
  before:animate-pulse`;

const Skeleton = forwardRef<DivElement, DivProps>(
  ({ className, ...props }, ref) => {
    const skeletonStyle = skeletonBaseStyle;

    return (
      <div className={cn(skeletonStyle, className)} ref={ref} {...props} />
    );
  }
);

export { Skeleton };
