"use client";

import { forwardRef } from "react";
import { Checkbox as BaseCheckbox } from "@lion-libs/react-ui-base";
import { cn } from "../../helper";

type TickIconProps = React.ComponentPropsWithoutRef<"svg"> & {
  color?: string;
};
const TickIcon = forwardRef<SVGSVGElement, TickIconProps>(
  ({ color = "#F03742", className, ...props }, ref) => (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="1em"
      height="1em"
      viewBox="0 0 66 50"
      type="check2"
      color={`${color}`}
      {...props}
      ref={ref}
    >
      <path
        d="M57.984 1.078l-3.53 3.563c-9.735 9.755-20.12 20.828-29.845 30.718L11.172 24.297l-3.875-3.188-6.344 7.72 3.844 3.187 17 14 3.531 2.906 3.188-3.219c10.769-10.792 22.435-23.413 33-34l3.53-3.562-7.062-7.063z"
        fill="currentColor"
      />
    </svg>
  )
);

type CheckboxRootProps = React.ComponentPropsWithoutRef<
  typeof BaseCheckbox.Root
> & {
  color:  "default" | "red";
};

type CheckboxRootElement = React.ElementRef<typeof BaseCheckbox.Root>;

const checkboxRootColorStyles = {
  default:"bg-white border-stone-400",
  red: "bg-white-500 border-red-500",
  none:"bg-white-500 border-transparent"
};

const checkboxRootBaseStyle =
  "border border-stone-400 w-5 h-5 rounded disabled:pointer-events-none disabled:bg-neutral-400 disabled:border-stone-400";

const CheckboxRoot = forwardRef<CheckboxRootElement, CheckboxRootProps>(
  (
    { className, children, color = "default", ...props },
    ref
  ) => {
    const checkboxStyles = `${checkboxRootBaseStyle} ${checkboxRootColorStyles[color]} `;
    return (
      <BaseCheckbox.Root
        className={cn(checkboxStyles, className)}
        ref={ref}
        {...props}
      >
        {children}
      </BaseCheckbox.Root>
    );
  }
);

type CheckboxIndicatorProps = React.ComponentPropsWithoutRef<
  typeof BaseCheckbox.Indicator
>;

type CheckboxIndicatorElement = React.ElementRef<typeof BaseCheckbox.Indicator>;
const checkboxIndicatorBaseStyle =
  "flex items-center justify-center text-current";

const CheckboxIndicator = forwardRef<
  CheckboxIndicatorElement,
  CheckboxIndicatorProps
>(({ className, children, ...props }, ref) => (
  <BaseCheckbox.Indicator
    ref={ref}
    className={cn(checkboxIndicatorBaseStyle, className)}
    {...props}
  >
    {children || <TickIcon />}
  </BaseCheckbox.Indicator>
));

const Checkbox = {
  Root: CheckboxRoot,
  Indicator: CheckboxIndicator,
  TickIcon,
};

export { Checkbox, CheckboxRoot, CheckboxIndicator, TickIcon };
