"use client";

import { forwardRef } from "react";
import { Dialog as BaseDialog } from "@lion-libs/react-ui-base";
import { cn } from "../../helper";

type DialogRootProps = React.ComponentPropsWithoutRef<typeof BaseDialog.Root>;
function DialogRoot(props: DialogRootProps) {
  return <BaseDialog.Root {...props} />;
}

type DialogTriggerElement = React.ElementRef<typeof BaseDialog.Trigger>;
type DialogTriggerProps = React.ComponentPropsWithoutRef<
  typeof BaseDialog.Trigger
>;
const DialogTrigger = forwardRef<DialogTriggerElement, DialogTriggerProps>(
  ({ ...props }, ref) => <BaseDialog.Trigger {...props} ref={ref} />
);

type DialogPortalProps = React.ComponentPropsWithoutRef<
  typeof BaseDialog.Portal
>;
function DialogPortal(props: DialogPortalProps) {
  return <BaseDialog.Portal {...props} />;
}

type Color = "transparent" | "black" | "slate";
type DialogOverlayElement = React.ElementRef<typeof BaseDialog.Overlay>;
type DialogOverlayProps = React.ComponentPropsWithoutRef<
  typeof BaseDialog.Overlay
> & {
  color?: Color | "black";
};

const dialogOverlayBaseStyles = "fixed inset-0";
const dialogOverlayColorStyles = {
  transparent: "bg-transparent",
  black: "bg-black-500/[0.2]",
  slate: "bg-slate-500",
};
const DialogOverlay = forwardRef<DialogOverlayElement, DialogOverlayProps>(
  ({ className, color = "black", ...props }, ref) => {
    const dialogOverlayStyles = `${dialogOverlayBaseStyles} ${dialogOverlayColorStyles[color]}`;
    return (
      <BaseDialog.Overlay
        className={cn(dialogOverlayStyles, className)}
        {...props}
        ref={ref}
      />
    );
  }
);

type DialogContentElement = React.ElementRef<typeof BaseDialog.Content>;
type DialogContentProps = React.ComponentPropsWithoutRef<
  typeof BaseDialog.Content
> & {
  radius?: boolean;
};
const baseDialogContentStyle =
  "bg-white-500 fixed top-[50%] left-[50%]  translate-x-[-50%] translate-y-[-50%]";
const DialogContent = forwardRef<DialogContentElement, DialogContentProps>(
  ({ className, radius = true, ...props }, ref) => {
    const dialogContentStyles = `${baseDialogContentStyle} ${
      radius && "rounded"
    }`;
    return (
      <BaseDialog.Content
        className={cn(dialogContentStyles, className)}
        {...props}
        ref={ref}
      />
    );
  }
);

type DialogCloseElement = React.ElementRef<typeof BaseDialog.Close>;
type DialogCloseProps = React.ComponentPropsWithoutRef<typeof BaseDialog.Close>;
const DialogClose = forwardRef<DialogCloseElement, DialogCloseProps>(
  ({ ...props }, ref) => <BaseDialog.Close {...props} ref={ref} />
);

type DialogTitleElement = React.ElementRef<typeof BaseDialog.Title>;
type DialogTitleProps = React.ComponentPropsWithoutRef<typeof BaseDialog.Title>;
const DialogTitle = forwardRef<DialogTitleElement, DialogTitleProps>(
  ({ ...props }, ref) => <BaseDialog.Title {...props} ref={ref} />
);

type DialogDescriptionElement = React.ElementRef<typeof BaseDialog.Description>;
type DialogDescriptionProps = React.ComponentPropsWithoutRef<
  typeof BaseDialog.Description
>;
const DialogDescription = forwardRef<
  DialogDescriptionElement,
  DialogDescriptionProps
>(({ ...props }, ref) => <BaseDialog.Description {...props} ref={ref} />);

const Dialog = {
  Root: DialogRoot,
  Trigger: DialogTrigger,
  Portal: DialogPortal,
  Overlay: DialogOverlay,
  Content: DialogContent,
  Close: DialogClose,
  Title: DialogTitle,
  Description: DialogDescription,
};

export {
  Dialog,
  DialogRoot,
  DialogTrigger,
  DialogPortal,
  DialogOverlay,
  DialogContent,
  DialogClose,
  DialogTitle,
  DialogDescription,
};
