"use client";

import {
  forwardRef,
  useRef,
  useImperativeHandle,
  useEffect,
  useLayoutEffect,
  useState,
  useContext,
  createContext,
  useMemo,
} from "react";
import { Slot } from "@radix-ui/react-slot";

import { cn } from "../../helper";

type PositionInfo = {
  top: number;
  height: number;
};
type ScrollSpyRootElement = React.ElementRef<"div">;
type ScrollSpyRootProps = React.ComponentPropsWithoutRef<"div"> & {
  scrollToHidden?: boolean;
};

const positionObj = {
  top: 0,
  height: 0,
};
const ScrollSpyRootContext = createContext<PositionInfo>(positionObj);
const ScrollSpyRootBaseStyle =
  "sticky top-0 w-full bg-white-500 shadow overflow-auto";
const ScrollSpyRoot = forwardRef<ScrollSpyRootElement, ScrollSpyRootProps>(
  ({ className, children, scrollToHidden = true, ...props }, ref) => {
    // setting scrollSpyRoot ref
    const rootRef = useRef<HTMLDivElement>(null);
    useImperativeHandle(ref, () => rootRef.current, []);

    // show scrollSpyRoot logic
    const [show, setShow] = useState(false);
    useLayoutEffect(() => {
      if (!scrollToHidden) return;

      const scrollHandler = () => {
        if (window.scrollY < 1) {
          setShow(false);
        } else {
          setShow(true);
        }
      };

      document.addEventListener("scroll", scrollHandler);
      return () => {
        document.removeEventListener("scroll", scrollHandler);
      };
    }, [scrollToHidden]);
    const showStyle = () => {
      if (!scrollToHidden) {
        return "block";
      }

      return show ? "block" : "hidden";
    };

    // get scrollSpyRoot elem height,top
    const [elemPositionInfo, setElemPositionInfo] = useState(positionObj);
    useEffect(() => {
      const rootEle = rootRef.current;
      if (!rootEle) return;
      const resizeObserver = new ResizeObserver((entries) => {
        const { contentRect, target } = entries[0];
        const height = contentRect?.height;
        const top = target?.getBoundingClientRect()?.top;

        setElemPositionInfo({ top, height });
      });

      resizeObserver.observe(rootEle);

      return () => {
        if (!rootEle) return;
        resizeObserver.unobserve(rootEle);
      };
    }, []);

    const { top, height } = elemPositionInfo;
    const contextValue = useMemo(() => ({ top, height }), [top, height]);

    return (
      <ScrollSpyRootContext.Provider value={contextValue}>
        <div
          className={cn(ScrollSpyRootBaseStyle, showStyle(), className)}
          ref={rootRef}
          {...props}
        >
          {children}
        </div>
      </ScrollSpyRootContext.Provider>
    );
  }
);

type ScrollSpyGroupElement = React.ElementRef<"ul">;
type ScrollSpyGroupProps = React.ComponentPropsWithoutRef<"ul">;
const ScrollSpyGroupBaseStyle =
  "h-full flex gap-3 justify-items-center tablet:overflow-x-auto";
const ScrollSpyGroup = forwardRef<ScrollSpyGroupElement, ScrollSpyGroupProps>(
  ({ className, children, ...props }, ref) => (
    <ul className={cn(ScrollSpyGroupBaseStyle, className)} ref={ref} {...props}>
      {children}
    </ul>
  )
);

type ScrollSpyContentElement = React.ElementRef<"li">;
type ScrollSpyContentProps = React.ComponentPropsWithoutRef<"li"> & {
  targetId?: string;
};
const ScrollSpyContentContext = createContext<{ active: boolean }>({
  active: false,
});
const scrollSpyContentBaseStyle = ` cursor-pointer flex items-center shrink-0 `;
const ScrollSpyContent = forwardRef<
  ScrollSpyContentElement,
  ScrollSpyContentProps
>(({ className, children, targetId = null, ...props }, ref) => {
  // get scrollSpy height
  const { top, height } = useContext(ScrollSpyRootContext);
  const totalHeight = top + height;
  const scrollIntoViewHandler = () => {
    if (!targetId) return;
    const selector = document.getElementById(targetId);
    if (!selector) return;
    const scrollToY =
      selector.getBoundingClientRect().top + window.scrollY - totalHeight;
    window.scrollTo({ top: scrollToY, behavior: "smooth" });
  };

  const [active, setActive] = useState(false);
  const contextValue = useMemo(
    () => ({
      active,
    }),
    [active]
  );
  useEffect(() => {
    const selector = document.getElementById(targetId);
    if (!selector) return;
    const options = {
      root: null,
      rootMargin: "-30% 0px -70% 0px",
    };
    const callback: IntersectionObserverCallback = (entries) => {
      const { isIntersecting } = entries[0];
      if (isIntersecting) {
        setActive(true);
      } else {
        setActive(false);
      }
    };
    const observer = new IntersectionObserver(callback, options);
    observer.observe(selector);
    return () => {
      observer.unobserve(selector);
    };
  }, [targetId]);

  return (
    <ScrollSpyContentContext.Provider value={contextValue}>
      <li
        className={cn(scrollSpyContentBaseStyle, className)}
        ref={ref}
        {...props}
      >
        <button type="button" onClick={scrollIntoViewHandler}>
          {children}
        </button>
      </li>
    </ScrollSpyContentContext.Provider>
  );
});

type ScrollSpyItemElement = React.ElementRef<"span">;
type ScrollSpyItemProps = React.ComponentPropsWithoutRef<"span"> & {
  asChild?: boolean;
  customActiveStyle?: string;
};

const baseActiveStyle = `relative font-bold before:content-[""] before:w-full before:border-b-[2px] before:border-red-500 before:rounded before:absolute before:bottom-[-5px]`;
const ScrollSpyItem = forwardRef<ScrollSpyItemElement, ScrollSpyItemProps>(
  (
    { asChild = false, className, customActiveStyle = "", children, ...props },
    ref
  ) => {
    const { active } = useContext(ScrollSpyContentContext);
    const activeStyle = active && (customActiveStyle || baseActiveStyle);
    const Comp = asChild ? Slot : "span";
    return (
      <Comp className={cn(className, activeStyle)} ref={ref} {...props}>
        {children}
      </Comp>
    );
  }
);

const ScrollSpy = {
  Root: ScrollSpyRoot,
  Group: ScrollSpyGroup,
  Content: ScrollSpyContent,
  Item: ScrollSpyItem,
};

export {
  ScrollSpy,
  ScrollSpyRoot,
  ScrollSpyGroup,
  ScrollSpyContent,
  ScrollSpyItem,
};
