"use client";

import { forwardRef } from "react";
import { cn } from "../../helper";

type Size = "sm" | "md" | "lg";
type DotElement = React.ElementRef<"div">;
type DotProps = React.ComponentPropsWithoutRef<"div"> & {
  size?: Size | "md";
};

const sizeStyles = {
  sm: {
    dotWrapper: "gap-1",
    dot: "w-1 h-1",
  },
  md: {
    dotWrapper: "gap-1",
    dot: "w-2 h-2",
  },
  lg: {
    dotWrapper: "gap-2",
    dot: "w-2 h-2",
  },
};
const dotBaseStyleArr = [
  "animation-delay-[-0.6s]",
  "animation-delay-[-0.5s]",
  "animation-delay-[-0.4s]",
  "animation-delay-[-0.3s]",
  "animation-delay-[-0.2s]",
  "animation-delay-[-0.1s]",
  "",
];

const dotWrapperBaseStyle = "inline-flex";
const dotBaseStyle = `bg-stone-400 rounded-full animate-dotFade`;

const LineLoader = forwardRef<DotElement, DotProps>(
  ({ size = "md", ...props }, ref) => {
    const dotWrapperSize = sizeStyles[size].dotWrapper;
    const dotSize = sizeStyles[size].dot;
    const dotWrapperStyle = `${dotWrapperBaseStyle} ${dotWrapperSize}`;
    const dotStyle = `${dotBaseStyle} ${dotSize}`;

    return (
      <div className={cn(dotWrapperStyle)} ref={ref} {...props}>
        {dotBaseStyleArr.map((style) => <div key={style} className={cn(dotStyle, style)} />)}
      </div>
    );
  }
);

export { LineLoader };
