"use client";

import { forwardRef } from "react";
import { Button } from "./button";
import { cn } from "../../helper";

type IconButtonElement = React.ElementRef<typeof Button>;
type IconButtonProps = React.ComponentPropsWithoutRef<typeof Button>;

const baseStyle = "p-0";

const sizeStyles = {
  xs: "h-5 w-5",
  sm: "h-7 w-7",
  md: "h-8 w-8 ",
  lg: "h-9 w-9",
  xl: "h-10 w-10",
};

const IconButton = forwardRef<IconButtonElement, IconButtonProps>(
  ({ children, className = "", size = "sm", ...props }, ref) => {
    const buttonStyles = `${baseStyle} ${sizeStyles[size]}`;
    return (
      <Button
        className={cn(`${buttonStyles} ${className}`)}
        ref={ref}
        {...props}
      >
        {children}
      </Button>
    );
  }
);

export { IconButton };
