"use client";

import { createContext, useCallback, useContext,forwardRef,  SetStateAction, Dispatch  } from 'react';
import { cn } from "../../helper";

/* -------------------------------------------------------------------------------------------------
 * SliderRoot
 * -----------------------------------------------------------------------------------------------*/

type SliderRootElement = React.ElementRef<'div'>;
type SliderRootProps = React.ComponentPropsWithoutRef<'div'> &{
  min:number;
  max:number;
  minValue: number;
  maxValue: number;
  setMinValue:Dispatch<SetStateAction<number>>;
  setMaxValue:Dispatch<SetStateAction<number>>;
}
type ContextType ={
  minValue: number;
  maxValue: number;
  posCaculator: (value: number) => number;
  handleMinChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  handleMaxChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
}
const SliderContext = createContext<ContextType>(null);

type UseSliderRootContextArgs={
  min:number;
  max:number;
  minValue: number;
  maxValue: number;
  setMinValue:Dispatch<SetStateAction<number>>;
  setMaxValue:Dispatch<SetStateAction<number>>;
}

const useSliderRootContext = ({minValue,setMinValue,maxValue,setMaxValue,min,max}:UseSliderRootContextArgs):ContextType => {
 
  const posCaculator = (value:number) => ((value - min) / (max - min)) *100 ;
  const handleMinChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    const newMinVal = Math.min(+e.target.value, maxValue - 1);
    setMinValue(newMinVal);
  };
  const handleMaxChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    const newMaxVal = Math.max(+e.target.value, minValue + 1);
    setMaxValue(newMaxVal);
  };
  return { minValue, maxValue, posCaculator, handleMinChange, handleMaxChange }
};

const SliderRoot = forwardRef<SliderRootElement, SliderRootProps>(
  (
    { children, className,min,max,minValue,setMinValue,maxValue,setMaxValue, ...props },
    ref
  ) => {
    const BaseSliderStyle = "relative flex item-center";

    return (
      <SliderContext.Provider
        value={useSliderRootContext({minValue,setMinValue,maxValue,setMaxValue,min,max})}>
        <div className={cn(BaseSliderStyle, className)}
          ref={ref} 
          {...props}>
          {children}
        </div>
      </SliderContext.Provider>
    );
  }
);

/* -------------------------------------------------------------------------------------------------
 * InputRange
 * -----------------------------------------------------------------------------------------------*/


type InputRangeElement = React.ElementRef<"input">;
type InputRangeProps = React.ComponentPropsWithoutRef<"input"> & {
  min: number;
  max: number;
  step: number;
  tag: "less" | "more";
};
const InputRange = forwardRef<InputRangeElement, InputRangeProps>(
  (
    { children, className, min, max, step, tag, ...props },
    ref
  ) => {
    const { minValue, maxValue, handleMaxChange, handleMinChange } = useContext(SliderContext)
    const handleChange = useCallback(
      (e: React.ChangeEvent<HTMLInputElement>): void => {
        if (tag === "less") {
          handleMinChange(e);
        } else {
          handleMaxChange(e);
        }
      },
      [tag, handleMinChange, handleMaxChange]
    );
    // thumb在最大值重疊時可以選到less thumb
    const BaseInputRangeStyle =
      `input-range h-full absolute apprance-none  w-full opacity-0 
      cursor-grab pointer-events-none ${maxValue === max && tag === "less" ? "z-30" : ""}`

    return (
      <input
        type="range"
        ref={ref}
        min={min}
        max={max}
        step={step}
        value={tag === "less" ? minValue : maxValue}
        onChange={handleChange}
        className={cn(BaseInputRangeStyle, className)}  {...props}
      />
    );
  }
);

/* -------------------------------------------------------------------------------------------------
 * Control
 * -----------------------------------------------------------------------------------------------*/

type ControlElement = React.ElementRef<"div">;
type ControlProps = React.ComponentPropsWithoutRef<"div"> & {
  tag: "less" | "more"; // 決定多和少的拉把
};
const Control = forwardRef<ControlElement, ControlProps>(
  (
    { children, className, tag, ...props },
    ref
  ) => {
    const BaseControlStyle =
      `absolute size-[18px] rounded-full border border-slate-200 
      top-1/2 z-20 translate-y-[-50%] bg-white-500`
    const { minValue, maxValue, posCaculator } = useContext(SliderContext)
    return (
      <div
        className={cn(BaseControlStyle, className)} ref={ref} {...props}
        style={{ left: `${tag === "less" ? posCaculator(minValue) : posCaculator(maxValue)}%` }}
      />
    );
  }
);

/* -------------------------------------------------------------------------------------------------
 * Rail
 * -----------------------------------------------------------------------------------------------*/

type RailElement = React.ElementRef<"div">;
type RailProps = React.ComponentPropsWithoutRef<"div"> 
const Rail = forwardRef<RailElement, RailProps>(
  (
    { children, className, ...props },
    ref
  ) => {
    const BaseRailStyle = "absolute w-full translate-y-[-50%] w-[calc(100%+16px)] bg-[#bebebe] h-[6px] rounded-2xl";
    return (
      <div className={cn(BaseRailStyle, className)}

        ref={ref} {...props}>
        {children}
      </div>
    );
  }
);

/* -------------------------------------------------------------------------------------------------
 * InnerRail
 * -----------------------------------------------------------------------------------------------*/

type InnerRailElement = React.ElementRef<"div">;
type InnerRailProps = React.ComponentPropsWithoutRef<"div"> 
const InnerRail = forwardRef<InnerRailElement, InnerRailProps>(
  (
    { className, ...props },
    ref
  ) => {
    const InnerRailStyle = "absolute top-0 h-full ";
    const { minValue, maxValue, posCaculator } = useContext(SliderContext)
    return (
      <div
        className={cn(InnerRailStyle, className)} ref={ref} {...props}
        style={{ left: `${posCaculator(minValue)}%`, right: `${100 - posCaculator(maxValue)}%` }}
      />
    );
  }
);




const Slider = {
  Root: SliderRoot,
  Input: InputRange,
  Control,
  Rail,
  InnerRail,

}


export { Slider, SliderRoot, InputRange, Control, Rail, InnerRail };