import { RefObject } from "react";

type Direction = "prev" | "next";
export const useClickScroll = (ref: RefObject<HTMLElement>) => {
  const handleClickScroll = (direction: Direction) => {
    if (!ref.current) return;
    const scrollDirection = direction === "prev" ? -1 : 1;
    const elementOffsetWidth = ref.current.offsetWidth; // 視窗寬度
    const moveDistance =
      scrollDirection * elementOffsetWidth + ref.current.scrollLeft; // 移動距離 + 當前位置
    ref.current.scrollTo({
      left: moveDistance,
      behavior: "smooth",
    });
  };

  return {
    handleClickScroll,
  };
};
