"use client";

import { forwardRef } from "react";
import { cn } from "../../helper";

type Size = "sm" | "md" | "lg";
type DotElement = React.ElementRef<"div">;
type DotProps = React.ComponentPropsWithoutRef<"div"> & {
  size?: Size | "md";
};

const sizeStyles = {
  sm: {
    dotWrapper: "w-8 h-8",
    dot: "before:w-1 before:h-1",
  },
  md: {
    dotWrapper: "w-12 h-12",
    dot: "before:w-2 before:h-2",
  },
  lg: {
    dotWrapper: "w-16 h-16",
    dot: "before:w-2 before:h-2",
  },
};
const dotBaseStyleArr = [
  "rotate-[0deg]",
  "rotate-[30deg] before:animation-delay-[-1.1s]",
  "rotate-[60deg] before:animation-delay-[-1.0s]",
  "rotate-[90deg] before:animation-delay-[-0.9s]",
  "rotate-[120deg] before:animation-delay-[-0.8s]",
  "rotate-[150deg] before:animation-delay-[-0.7s]",
  "rotate-[180deg] before:animation-delay-[-0.6s]",
  "rotate-[210deg] before:animation-delay-[-0.5s]",
  "rotate-[240deg] before:animation-delay-[-0.4s]",
  "rotate-[270deg] before:animation-delay-[-0.3s] ",
  "rotate-[300deg] before:animation-delay-[-0.2s]",
  "rotate-[330deg] before:animation-delay-[-0.1s]",
];

const dotWrapperBaseStyle = "relative";
const dotBaseStyle = `
absolute top-0 left-0 w-full h-full  
before:content-['']  before:bg-stone-400 before:rounded-full before:block before:my-0 before:mx-auto before:animate-dotFade`;

const CircleLoader = forwardRef<DotElement, DotProps>(
  ({ size = "md", ...props }, ref) => {
    const dotWrapperSize = sizeStyles[size].dotWrapper;
    const dotSize = sizeStyles[size].dot;
    const dotWrapperStyle = `${dotWrapperBaseStyle} ${dotWrapperSize}`;
    const dotStyle = `${dotBaseStyle} ${dotSize}`;

    return (
      <div className={cn(dotWrapperStyle)} ref={ref} {...props}>
        {dotBaseStyleArr.map((style) => <div key={style} className={cn(`${dotStyle} ${style}`)} />)}
      </div>
    );
  }
);

export { CircleLoader };
