"use client";

import { forwardRef } from "react";
import { Collapsible as BaseCollapsible } from "@lion-libs/react-ui-base";
import { cn } from "../../helper";

type CollapsibleProps = React.ComponentPropsWithoutRef<
  typeof BaseCollapsible.Root
>;

type CollapsibleRootElement = React.ElementRef<typeof BaseCollapsible.Root>;

const collapsibleBaseStyle = "rounded bg-white-500";

const CollapsibleRoot = forwardRef<CollapsibleRootElement, CollapsibleProps>(
  ({ className, children, ...props }, ref) => {
    const collapsibleStyle = `${collapsibleBaseStyle}`;

    return (
      <BaseCollapsible.Root
        className={cn(collapsibleStyle, className)}
        ref={ref}
        {...props}
      >
        {children}
      </BaseCollapsible.Root>
    );
  }
);

type CollapsibleOverlayElement = React.ElementRef<"div">;

type CollapsibleOverlayProps = React.ComponentPropsWithoutRef<"div"> & {
  variant?: "default" | "vague" | "";
};

const collapsibleOverlayBaseStyle = "relative";

const collapsibleOverlayVariantStyle = {
  "": "",
  default: "",
  vague: `
  before:content-[''] before:pointer-events-none before:absolute 
  before:bg-gradient-to-b from-transparent to-white-500 before:bottom-0 before:w-full before:h-[50%]`,
};

const CollapsibleOverlay = forwardRef<
  CollapsibleOverlayElement,
  CollapsibleOverlayProps
>(({ className, children, variant = "default", ...props }, ref) => {
  const collapsibleOverlayStyle = `${collapsibleOverlayBaseStyle} ${collapsibleOverlayVariantStyle[variant]}`;

  return (
    <div
      className={cn(collapsibleOverlayStyle, className)}
      ref={ref}
      {...props}
    >
      {children}
    </div>
  );
});

type CollapsibleTriggerProps = React.ComponentPropsWithoutRef<
  typeof BaseCollapsible.Trigger
> & {
  color?: "gray" | "blue";
  size?: "default" | "lg";
};

type CollapsibleTriggerRootElement = React.ElementRef<
  typeof BaseCollapsible.Trigger
>;

const CollapsibleTriggerVariantStyle = {
  gray: {
    default: "text-sm text-black-500",
    lg: "text-base text-black-500 gap-1",
  },
  blue: {
    default: "text-sm text-blue-500",
    lg: "text-base text-blue-500 gap-1 ",
  },
};

const CollapsibleTriggerBaseStyle = "flex items-center text-sm text-left";

const CollapsibleTrigger = forwardRef<
  CollapsibleTriggerRootElement,
  CollapsibleTriggerProps
>(
  (
    { className, color = "blue", size = "default", children, ...props },
    ref
  ) => {
    const collapsibleTriggerStyle = `${CollapsibleTriggerBaseStyle} ${CollapsibleTriggerVariantStyle[color][size]}`;
    return (
      <BaseCollapsible.Trigger
        className={cn(collapsibleTriggerStyle, className)}
        ref={ref}
        {...props}
      >
        {children}
      </BaseCollapsible.Trigger>
    );
  }
);

type CollapsibleContentProps = React.ComponentPropsWithoutRef<
  typeof BaseCollapsible.Content
>;

type CollapsibleContentRootElement = React.ElementRef<
  typeof BaseCollapsible.Content
>;

const collapsibleContentBaseStyle = "text-sm";
const CollapsibleContent = forwardRef<
  CollapsibleContentRootElement,
  CollapsibleContentProps
>(({ className, children, ...props }, ref) => {
  const CollapsibleContentStyle = `${collapsibleContentBaseStyle}`;
  return (
    <BaseCollapsible.Content
      className={cn(CollapsibleContentStyle, className)}
      ref={ref}
      {...props}
    >
      {children}
    </BaseCollapsible.Content>
  );
});

const Collapsible = {
  Root: CollapsibleRoot,
  Overlay: CollapsibleOverlay,
  Trigger: CollapsibleTrigger,
  Content: CollapsibleContent,
};

export {
  Collapsible,
  CollapsibleRoot,
  CollapsibleOverlay,
  CollapsibleTrigger,
  CollapsibleContent,
};
