"use client";

import { forwardRef, useState } from 'react';
import { cn } from '../../helper';

// 定義 IconProps 型別
type IconProps = React.SVGAttributes<SVGElement> & {
    isActive: boolean;
    onClick?: () => void;
    className?: string;
};

// 定義 RateProps 型別
type RateProps = React.ComponentPropsWithoutRef<'div'> & {
    initialRating?: number; // 初始星星
    onRatingChange?: (rating: number) => void; // 評分改變的function
    maxRating?: number; // 最大星星數
    hideEmpty?: boolean; // 加這個就會變成寫死的星星
    starSVGStyle?: {
        active?: string;
        empty?: string;
    } // 星星顏色
    customActiveEl?: React.ReactNode;
    customEmptyEl?: React.ReactNode;
};

// 定義 StarIcon 
const StarIcon = forwardRef<SVGSVGElement, IconProps>(({ isActive, onClick, className }, ref) => {
    const activeStyle = "stroke-[#FFBA00] fill-[#FFBA00]";
    const emptyStyle = "stroke-[#BBBBBB] fill-none";


    return (
        <svg
            xmlns="http://www.w3.org/2000/svg"
            width="26.25"
            height="26.25"
            viewBox="0 0 26.25 26.25"
            strokeWidth="2"
            className={cn(isActive ? activeStyle : emptyStyle, className)}
            onClick={onClick}
            ref={ref}
        >
            <polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2" />
        </svg >
    );
});

const rateBaseStyle = "inline-flex items-center gap-1";

// 定義 Rate 
const Rate = forwardRef<React.ElementRef<'div'>, RateProps>(
    ({ initialRating = 0, maxRating = 5, onRatingChange, hideEmpty = false, customActiveEl, customEmptyEl, className, starSVGStyle, ...props }, ref) => {
        const [rating, setRating] = useState(initialRating);

        // 更新星星數量的函數
        const handleStarClick = (index: number) => {
            if (hideEmpty) return;
            const newRating = index + 1;
            setRating(newRating);
            if (onRatingChange) onRatingChange(newRating);
        };

        function renderRate(isActive: boolean) {
            const activeEl = customActiveEl ?? <StarIcon isActive={isActive} className={starSVGStyle?.active} />;
            const emptyEl = customEmptyEl ?? <StarIcon isActive={isActive} className={starSVGStyle?.empty} />;
            return isActive ? activeEl : emptyEl;
        }

        const starCount = hideEmpty ? initialRating : maxRating;
        return (
            <div ref={ref} className={cn(rateBaseStyle, className)} {...props}>
                {Array.from({ length: starCount }, (_, index) => (
                    <button type='button' key={index} onClick={() => handleStarClick(index)} className={cn('cursor-pointer', { 'cursor-default': hideEmpty })}>
                        {renderRate(index < rating)}
                    </button>
                ))}
            </div>
        );
    }
);


export { Rate };
