const createSpacingObj = (n: number, space: number) => {
  const result: { [key: string]: string } = {};

  for (let i = 0; i < n; i+=1) {
    result[i] = i * space === 0 ? "0" : `${i * space}px`;
  }

  return result;
};

const spacing = createSpacingObj(100, 5);

export default {
  spacing,
};
