"use client";

import { forwardRef, createContext, useMemo, useContext } from "react";
import { Tabs as BaseTabs } from "@lion-libs/react-ui-base";
import { cn } from "../../helper";

type Variant =
  | "searchBar"
  | "searchBarBtn"
  | "searchBarBtnLg"
  | "contentBtnGroup"
  | "searchBarUnderline"
  | "contentUnderline"
  | "contentUnderlineAndDraw"
  | "searchBarUnderlineLg";
type TabsRootElement = React.ElementRef<typeof BaseTabs.Root>;
type TabsRootProps = React.ComponentPropsWithoutRef<typeof BaseTabs.Root> & {
  variant?: Variant;
};
type TabsRootContextValue = {
  variant?: Variant;
};
const tabsRootBaseStyles = "";
const TabsRootContext = createContext<TabsRootContextValue>(null);
const TabsRoot = forwardRef<TabsRootElement, TabsRootProps>(
  ({ className, children, variant, ...props }, ref) => {
    const contextValue = useMemo(
      () => ({
        variant,
      }),
      [variant]
    );

    return (
      <TabsRootContext.Provider value={contextValue}>
        <BaseTabs.Root
          ref={ref}
          {...props}
          className={cn(`${tabsRootBaseStyles}`, className)}
        >
          {children}
        </BaseTabs.Root>
      </TabsRootContext.Provider>
    );
  }
);

type TabsListElement = React.ElementRef<typeof BaseTabs.List>;
type TabsListProps = React.ComponentPropsWithoutRef<typeof BaseTabs.List>;
const tabsListBaseStyles =
  "w-full flex item-center text-white mb-3 overflow-x-auto";
const tabsListVariantStyles = {
  searchBar: "bg-transparent",
  searchBarBtn: "gap-2",
  searchBarBtnLg: "gap-2",
  contentBtnGroup: "shadow w-max rounded",
  searchBarUnderline: "",
  contentUnderline: "",
  contentUnderlineAndDraw:
    "flex-col gap-2 phone:flex phone:flex-row phone:gap-3 phone:bg-neutral-300",
  searchBarUnderlineLg: "",
};

const TabsList = forwardRef<TabsListElement, TabsListProps>(
  ({ className, children, ...props }, ref) => {
    const tabsRootApi = useContext(TabsRootContext);
    const { variant } = tabsRootApi;
    return (
      <BaseTabs.List
        ref={ref}
        {...props}
        className={cn(
          `${tabsListBaseStyles} ${tabsListVariantStyles[variant]}`,
          className
        )}
      >
        {children}
      </BaseTabs.List>
    );
  }
);

type TabsTriggerElement = React.ElementRef<typeof BaseTabs.Trigger>;
type TabsTriggerProps = React.ComponentPropsWithoutRef<typeof BaseTabs.Trigger>;
const tabsTriggerBaseStyles = {
  searchBar: `grow h-10 after:w-[calc(100%_-_theme(spacing.2))] relative text-sm font-400 data-[state=active]:before:bg-red-500 before:w-1 before:h-1 before:rounded-full before:absolute before:top-1 before:right-[3px] data-[state=active]:after:bg-red-500  after:h-[3px] after:absolute after:bottom-0 after:inset-x-0 after:mx-auto after:rounded  data-[state=active]:rounded-t data-[state=inactive]:border-b data-[state=active]:border-x data-[state=active]:border-t border-stone-300 phone:text-base phone:font-bold flex items-center justify-center wrap min-w-10 px-1  phone:text-white-500 phone:bg-black-500/[.75] phone:data-[state=active]:text-red-500 phone:data-[state=active]:bg-white-500 phone:data-[state=active]:border-t-4 phone:data-[state=active]:border-red-500 phone:data-[state=active]:border-x-0 phone:data-[state=active]:rounded-[0] phone:before:hidden phone:after:hidden phone:data-[state=inactive]:border-b-0 `,
  searchBarBtn:
    "h-6 w-19 shrink-0 text-slate-500 text-sm rounded border border-stone-300 data-[state=active]:bg-red-500 data-[state=active]:text-white-500 data-[state=active]:border-transparent",
  searchBarBtnLg:
    "h-7 w-20 shrink-0 text-slate-500 text-sm rounded border border-stone-300 data-[state=active]:bg-red-500 data-[state=active]:text-white-500 data-[state=active]:border-transparent",
  contentBtnGroup:
    "h-6 w-20 shrink-0 text-slate-500 text-sm border-r last:border-0 border-stone-300 data-[state=active]:text-red-500 ",
  searchBarUnderline:
    "h-7 px-2 shrink-0 text-slate-500 text-sm border-b border-stone-300 data-[state=active]:text-red-500 data-[state=active]:border-red-500 data-[state=active]:border-b-2",
  contentUnderline:
    "h-8 px-2 shrink-0 text-slate-500 text-sm border-b border-stone-300 data-[state=active]:text-red-500 data-[state=active]:border-red-500 data-[state=active]:border-b-2",
  contentUnderlineAndDraw:
    "h-9 flex rounded justify-between items-center px-2 shrink-0 shadow text-base font-bold text-slate-500 phone:text-sm phone:font-400 phone:h-8 phone:data-[state=active]:text-red-500 phone:data-[state=active]:border-red-500 phone:data-[state=active]:border-b-2 phone:shadow-[0_0_0_0_rgba(0,0,0,0)] phone:rounded-[0px] phone:justify-center",
  searchBarUnderlineLg:
    "h-8 px-2 shrink-0 text-slate-500 text-sm border-b border-stone-300 data-[state=active]:text-red-500 data-[state=active]:border-red-500 data-[state=active]:border-b-2",
};
const TabsTrigger = forwardRef<TabsTriggerElement, TabsTriggerProps>(
  ({ className, children, ...props }, ref) => {
    const tabsRootApi = useContext(TabsRootContext);
    const { variant } = tabsRootApi;
    return (
      <BaseTabs.Trigger
        ref={ref}
        {...props}
        className={cn(`${tabsTriggerBaseStyles[variant]}`, className)}
      >
        {children}
      </BaseTabs.Trigger>
    );
  }
);
type TabsContentElement = React.ElementRef<typeof BaseTabs.Content>;
type TabsContentProps = React.ComponentPropsWithoutRef<typeof BaseTabs.Content>;
const tabsContentBaseStyles = "bg-neutral-400 rounded p-2";
const TabsContent = forwardRef<TabsContentElement, TabsContentProps>(
  ({ className, children, ...props }, ref) => (
    <BaseTabs.Content
      ref={ref}
      {...props}
      className={cn(`${tabsContentBaseStyles}`, className)}
    >
      {children}
    </BaseTabs.Content>
  )
);

const Tabs = {
  Root: TabsRoot,
  List: TabsList,
  Trigger: TabsTrigger,
  Content: TabsContent,
};
export { Tabs, TabsRoot, TabsList, TabsTrigger, TabsContent };
