'use client';

import { forwardRef } from 'react';
import { RadioGroup as BaseRadioGroup } from '@lion-libs/react-ui-base';
import { cn } from '../../helper';

type RadioDotIconProps = React.ComponentPropsWithoutRef<'svg'> & {
  color?: string;
};

const RadioDotIcon = forwardRef<SVGSVGElement, RadioDotIconProps>(
  ({ color = '#F03742', ...props }, ref) => (
    <svg
      width="10"
      height="10"
      viewBox="0 0 16 16"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      {...props}
      ref={ref}
    >
      <path
        fillRule="evenodd"
        clipRule="evenodd"
        d="M15.5 8.30423C15.5 12.4464 12.1421 15.8042 8 15.8042C3.85786 15.8042 0.5 12.4464 0.5 8.30423C0.5 4.1621 3.85786 0.804233 8 0.804233C12.1421 0.804233 15.5 4.1621 15.5 8.30423Z"
        fill={color}
      />
    </svg>
  )
);

type RadioGroupRootProps = React.ComponentPropsWithoutRef<
  typeof BaseRadioGroup.Root
>;

type RadioGroupRootElement = React.ElementRef<typeof BaseRadioGroup.Root>;

const radioGroupRootBaseStyles = ' flex flex-col gap-1 text-slate-500';

const RadioGroupRoot = forwardRef<RadioGroupRootElement, RadioGroupRootProps>(
  ({ className, children, ...props }, ref) => (
    <BaseRadioGroup.Root
      className={cn(`${radioGroupRootBaseStyles}`, className)}
      ref={ref}
      {...props}
    >
      {children}
    </BaseRadioGroup.Root>
  )
);

type RadioGroupItemProps = React.ComponentPropsWithoutRef<
  typeof BaseRadioGroup.Item
>;

type RadioGroupItemElement = React.ElementRef<typeof BaseRadioGroup.Item>;
const radioGroupItemBaseStyles =
  'aspect-square h-4 w-4 rounded-full border border-stone-300 text focus:outline-none disabled:cursor-not-allowed ';
const radioGroupIndicatorBaseStyles = 'flex items-center justify-center';

const RadioGroupItem = forwardRef<RadioGroupItemElement, RadioGroupItemProps>(
  ({ className, children, ...props }, ref) => (
    <BaseRadioGroup.Item
      className={cn(`${radioGroupItemBaseStyles}`, className)}
      ref={ref}
      {...props}
    >
      <BaseRadioGroup.Indicator className={`${radioGroupIndicatorBaseStyles}`}>
        {children || <RadioDotIcon />}
      </BaseRadioGroup.Indicator>
    </BaseRadioGroup.Item>
  )
);

const RadioGroup = {
  Root: RadioGroupRoot,
  Item: RadioGroupItem,
  Dot: RadioDotIcon
};

export { RadioGroup, RadioGroupRoot, RadioGroupItem, RadioDotIcon };
