"use client";

import { forwardRef } from "react";
import { Input as BaseInput } from "@lion-libs/react-ui-base";
import { cn } from "../../helper";

type ButtonElement = React.ElementRef<typeof BaseInput>;
type ButtonProps = React.ComponentPropsWithoutRef<typeof BaseInput> & {
  color: "gray" | "white";
  variant: "solid" | "none" | "alert";
};

const inputColorStyles = {
  white: {
    solid: "bg-white-500 border-stone-400",
    alert: "bg-white-400 border-red-500",
    none: "bg-white-500 border-transparent",
  },
  gray: {
    solid: "bg-neutral-400 border-stone-400",
    alert: "bg-white-400 border-red-500",
    none: "bg-neutral-400 border-transparent",
  },
};
const inputBaseStyles =
  "p-3 h-8 box-border border text-slate-400 rounded focus:outline-none disabled:bg-neutral-400";

const Input = forwardRef<ButtonElement, ButtonProps>(
  ({ className, color = "gray", variant = "solid", ...props }, ref) => {
    const inputStyles = `${inputColorStyles[color][variant]} ${inputBaseStyles} `;
    return (
      <BaseInput className={cn(inputStyles, className)} ref={ref} {...props} />
    );
  }
);

export { Input };
