"use client";

import { forwardRef } from "react";
import { Slot } from "@radix-ui/react-slot";
import {
    Pagination as BasePagination,
    Button,
    CalcPaginationItem,
} from "@lion-libs/react-ui-base";
import { cn } from "../../helper";

const paginationButtonStyle = {
    default:
        "flex items-center justify-center w-10 h-[35px] hover:bg-red-500 hover:text-white-500 rounded ",
    active: "w-10 h-[35px] bg-red-500 text-white-500 rounded",
    disabled:
        "flex items-center justify-center w-10 h-[35px] hover:bg-blue-500 text-stone-400 cursor-default rounded",
};

// Pagination Root
type PaginationRootElement = React.ElementRef<typeof BasePagination.Root>;
type PaginationRootProps = React.ComponentPropsWithoutRef<
    typeof BasePagination.Root
> & {
    asChild?: boolean;
};
const PaginationRoot = forwardRef<PaginationRootElement, PaginationRootProps>(
    ({ asChild = false, children, className, ...props }, ref) => {
        const Comp = asChild ? Slot : "div";
        return (
            <Comp className={className} ref={ref} {...props}>
                {children}
            </Comp>
        );
    }
);

// Pagination Content
type PaginationContentElement = React.ElementRef<typeof BasePagination.Content>;
type PaginationContentProps = React.ComponentPropsWithoutRef<
    typeof BasePagination.Content
> & {
    asChild?: boolean;
};
const PaginationContent = forwardRef<
    PaginationContentElement,
    PaginationContentProps
>(({ asChild = false, children, className, ...props }, ref) => {
    const Comp = asChild ? Slot : "div";
    return (
        <Comp className={className} ref={ref} {...props}>
            {children}
        </Comp>
    );
});

// Pagination Item
type PaginationItemElement = React.ElementRef<typeof BasePagination.Item>;
type PaginationItemProps = React.ComponentPropsWithoutRef<
    typeof BasePagination.Item
> & {
    asChild?: boolean;
};
const PaginationItem = forwardRef<PaginationItemElement, PaginationItemProps>(
    ({ children, className, ...props }, ref) => (
        <Button className={className} ref={ref} {...props}>
            {children}
        </Button>
    )
);

// Pagination Previous
type PaginationPreviousElement = React.ElementRef<
    typeof BasePagination.Previous
>;
type PaginationPreviousProps = React.ComponentPropsWithoutRef<
    typeof BasePagination.Previous
> & {
    asChild?: boolean;
    href?: string;
};
const PaginationPrevious = forwardRef<
    PaginationPreviousElement,
    PaginationPreviousProps
>(({ href, children, className, ...props }, ref) => (
    <div
        className={cn(
            className,
            "w-full h-full flex items-center justify-center"
        )}
        ref={ref}
        {...props}
    >
        {children || (
            <svg
                width="16"
                height="16"
                viewBox="0 0 16 16"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
            >
                <path
                    d="M10.42 11.58L9.57162 12.4287L5.57031 8.42867C5.33591 8.19434 5.33591 7.81433 5.57031 7.58L9.57162 3.58L10.42 4.42867L6.84317 8.00434L10.42 11.58Z"
                    fill="currentColor"
                />
            </svg>
        )}
    </div>
));

// Pagination Next
type PaginationNextElement = React.ElementRef<typeof BasePagination.Next>;
type PaginationNextProps = React.ComponentPropsWithoutRef<
    typeof BasePagination.Next
> & {
    asChild?: boolean;
    href?: string;
};
const PaginationNext = forwardRef<PaginationNextElement, PaginationNextProps>(
    ({ href, children, className, ...props }, ref) => (
        <div
            className={cn(
                className,
                "w-full h-full flex items-center justify-center"
            )}
            ref={ref}
            {...props}
        >
            {children || (
                <svg
                    width="16"
                    height="16"
                    viewBox="0 0 16 16"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <path
                        d="M5.57999 4.42L6.42838 3.57133L10.4297 7.57133C10.6641 7.80566 10.6641 8.18567 10.4297 8.42L6.42838 12.42L5.57999 11.5713L9.15683 7.99566L5.57999 4.42Z"
                        fill="currentColor"
                    />
                </svg>
            )}
        </div>
    )
);

const Pagination = {
    Root: PaginationRoot,
    Content: PaginationContent,
    Item: PaginationItem,
    Previous: PaginationPrevious,
    Next: PaginationNext,
};

export {
    Pagination,
    PaginationRoot,
    PaginationContent,
    PaginationItem,
    PaginationPrevious,
    PaginationNext,
};

export { CalcPaginationItem, paginationButtonStyle };
