"use client";

import { forwardRef } from "react";
import { Toast as baseToast } from "@lion-libs/react-ui-base";
import { cn } from "../../helper";

const ToastProvider = baseToast.Provider;

type Position =
  | "topLeft"
  | "topCenter"
  | "topRight"
  | "center"
  | "bottomLeft"
  | "bottomCenter"
  | "bottomRight";

type ToastViewportElement = React.ElementRef<typeof baseToast.Viewport>;
type ToastViewportProps = React.ComponentPropsWithoutRef<
  typeof baseToast.Viewport
> & {
  position?: Position;
};

const toastViewportBaseStyle = `
pointer-events-none fixed  z-[100] max-h-screen w-full`;

const toastViewPositionStyle = {
  topLeft: "top-0 left-0",
  topCenter: "top-0 left-1/2 translate-x-[-50%]",
  topRight: "top-0 right-0",
  center: "top-1/2 left-1/2 translate-x-[-50%] translate-y-[-50%]",
  bottomLeft: "bottom-0 left-0",
  bottomCenter: "bottom-0 left-1/2 translate-x-[-50%]",
  bottomRight: "bottom-0 right-0",
};

const ToastViewport = forwardRef<ToastViewportElement, ToastViewportProps>(
  ({ children, className, position = "center", ...props }, ref) => {
    const toastViewportStyle = `${toastViewportBaseStyle} ${toastViewPositionStyle[position]}`;
    return (
      <baseToast.Viewport
        ref={ref}
        className={cn(toastViewportStyle, className)}
        {...props}
      >
        {children}
      </baseToast.Viewport>
    );
  }
);

type ToastRootElement = React.ElementRef<typeof baseToast.Root>;
type ToastRootProps = React.ComponentPropsWithoutRef<typeof baseToast.Root>;

const toastRootBaseStyle = `
relative flex justify-center items-center 
w-full h-full bg-white-500 shadow rounded 
data-[state=open]:animate-in data-[state=closed]:animate-out`;

const ToastRoot = forwardRef<ToastRootElement, ToastRootProps>(
  ({ children, className, duration = 1000, ...props }, ref) => {
    const toastRootStyle = toastRootBaseStyle;
    return (
      <baseToast.Root
        ref={ref}
        duration={duration}
        className={cn(toastRootStyle, className)}
        {...props}
      >
        {children}
      </baseToast.Root>
    );
  }
);

type ToastTitleElement = React.ElementRef<typeof baseToast.Title>;
type ToastTitleProps = React.ComponentPropsWithoutRef<typeof baseToast.Title>;

const ToastTitle = forwardRef<ToastTitleElement, ToastTitleProps>(
  ({ children, className, ...props }, ref) => (
    <baseToast.Title ref={ref} className={cn(className)} {...props}>
      {children}
    </baseToast.Title>
  )
);

const toastDescriptionBaseStyle = "flex items-center gap-1";

type ToastDescriptionElement = React.ElementRef<typeof baseToast.Description>;
type ToastDescriptionProps = React.ComponentPropsWithoutRef<
  typeof baseToast.Description
>;

const ToastDescription = forwardRef<
  ToastDescriptionElement,
  ToastDescriptionProps
>(({ children, className, ...props }, ref) => {
  const toastDescriptionStyle = toastDescriptionBaseStyle;
  return (
    <baseToast.Description
      ref={ref}
      className={cn(toastDescriptionStyle, className)}
      {...props}
    >
      {children}
    </baseToast.Description>
  );
});

type ToastActionElement = React.ElementRef<typeof baseToast.Action>;
type ToastActionProps = React.ComponentPropsWithoutRef<typeof baseToast.Action>;

const ToastAction = forwardRef<ToastActionElement, ToastActionProps>(
  ({ children, className, ...props }, ref) => (
    <baseToast.Action ref={ref} className={cn(className)} {...props}>
      {children}
    </baseToast.Action>
  )
);

type ToastCloseElement = React.ElementRef<typeof baseToast.Close>;
type ToastCloseProps = React.ComponentPropsWithoutRef<typeof baseToast.Close>;

const toastCloseBaseStyle = "absolute right-1 top-0 pointer-events-auto";

const ToastClose = forwardRef<ToastCloseElement, ToastCloseProps>(
  ({ children, className, ...props }, ref) => {
    const toastCloseStyle = toastCloseBaseStyle;
    return (
      <baseToast.Close
        ref={ref}
        className={cn(toastCloseStyle, className)}
        {...props}
      >
        {children || <div>&#x2716;</div>}
      </baseToast.Close>
    );
  }
);

const Toast = {
  Provider: ToastProvider,
  Viewport: ToastViewport,
  Root: ToastRoot,
  Title: ToastTitle,
  Description: ToastDescription,
  Action: ToastAction,
  Close: ToastClose,
};

export {
  Toast,
  ToastProvider,
  ToastViewport,
  ToastRoot,
  ToastTitle,
  ToastDescription,
  ToastAction,
  ToastClose,
};
