'use client';

import { forwardRef } from 'react';
import { Card as BaseCard } from '@lion-libs/react-ui-base';
import { cn } from '../../helper';

type Variant = 'border' | 'primaryShadow' | 'deepShadow' | 'ghost';
type Direction = 'row' | 'col';

type CardRootElement = React.ElementRef<typeof BaseCard.Root>;
type CardRootProps = React.ComponentPropsWithoutRef<typeof BaseCard.Root> & {
  variant?: Variant | 'border';
  direction?: Direction | 'row';
};

const cardRootBaseStyle = 'relative inline-flex items-center p-3 rounded';

const cardRootDirectionStyle = {
  row: 'flex-row',
  col: 'flex-col'
};

const cardRootBorderStyle = {
  border: 'border border-slate-400',
  primaryShadow: 'shadow',
  deepShadow: 'shadow-deep',
  ghost: 'bg-neutral-300/[0.2]'
};

const CardRoot = forwardRef<CardRootElement, CardRootProps>(
  (
    { children, className, variant = 'border', direction = 'col', ...props },
    ref
  ) => {
    const cardStyle = `${cardRootBaseStyle} ${cardRootDirectionStyle[direction]} ${cardRootBorderStyle[variant]}`;

    return (
      <BaseCard.Root className={cn(cardStyle, className)} ref={ref} {...props}>
        {children}
      </BaseCard.Root>
    );
  }
);

const cardHeaderDecorateStyle = `before:absolute before:left-0 before:top-3 before:w-1 before:h-5 before:block before:bg-red-500 before:rounded-r`;
type CardHeaderElement = React.ElementRef<typeof BaseCard.Header>;
type CardHeaderProps = React.ComponentPropsWithoutRef<
  typeof BaseCard.Header
> & {
  isDecorate?: boolean; // 紅色裝飾線
};
const CardHeader = forwardRef<CardHeaderElement, CardHeaderProps>(
  ({ children, className, isDecorate = false, ...props }, ref) => {
    const cardHeaderStyle = `${isDecorate && cardHeaderDecorateStyle}`;
    return (
      <BaseCard.Header
        className={cn(cardHeaderStyle, className)}
        ref={ref}
        {...props}
      >
        {children}
      </BaseCard.Header>
    );
  }
);

type CardBodyElement = React.ElementRef<typeof BaseCard.Body>;
type CardBodyProps = React.ComponentPropsWithoutRef<typeof BaseCard.Body>;
const CardBody = forwardRef<CardBodyElement, CardBodyProps>(
  ({ children, className, ...props }, ref) => (
    <BaseCard.Body className={className} ref={ref} {...props}>
      {children}
    </BaseCard.Body>
  )
);

type CardFooterElement = React.ElementRef<typeof BaseCard.Footer>;
type CardFooterProps = React.ComponentPropsWithoutRef<typeof BaseCard.Footer>;
const CardFooter = forwardRef<CardFooterElement, CardFooterProps>(
  ({ children, className, ...props }, ref) => (
    <BaseCard.Footer className={className} ref={ref} {...props}>
      {children}
    </BaseCard.Footer>
  )
);

const Card = {
  Root: CardRoot,
  Header: CardHeader,
  Body: CardBody,
  Footer: CardFooter
};

export { Card, CardRoot, CardBody, CardFooter, CardHeader };
