import { RefObject } from "react";

export const useDragScroll = (
  ref: RefObject<HTMLElement>,
  options: {
    direction?: "vertical" | "horizontal" | "both";
  } = { direction: "both" }
) => {
  const { direction } = options;
  let startPosition = {
    scrollLeft: 0, // 取得滾動條距離
    scrollTop: 0,
    mouseX: 0, // 滑鼠的座標
    mouseY: 0,
  };

  const handleMouseMove = (event: { clientX: number; clientY: number }) => {
    if (!ref.current) return;
    const moveDistanceX = event.clientX - startPosition.mouseX;
    const moveDistanceY = event.clientY - startPosition.mouseY;
    if (direction !== "horizontal") {
      // eslint-disable-next-line no-param-reassign
      ref.current.scrollTop = startPosition.scrollTop - moveDistanceY;
    }
    if (direction !== "vertical") {
      // eslint-disable-next-line no-param-reassign
      ref.current.scrollLeft = startPosition.scrollLeft - moveDistanceX;
    }
  };

  const handleMouseUp = () => {
    // eslint-disable-next-line no-param-reassign
    document.removeEventListener("mousemove", handleMouseMove);
    document.removeEventListener("mouseup", handleMouseUp);
  };

  const handleMouseDown = (event: { clientX: number; clientY: number }) => {
    if (!ref.current) return;
    startPosition = {
      scrollLeft: ref.current.scrollLeft || 0,
      scrollTop: ref.current.scrollTop || 0,
      mouseX: event.clientX,
      mouseY: event.clientY,
    };

    document.addEventListener("mousemove", handleMouseMove);
    document.addEventListener("mouseup", handleMouseUp);
  };

  return { handleMouseDown };
};
