/* eslint-disable max-lines */

"use client";

import {
  Fragment,
  forwardRef,
  createContext,
  useImperativeHandle,
  useRef,
  useContext,
  useMemo,
  useState,
  useEffect,
} from "react";
import { Slot } from "@radix-ui/react-slot";
import { cn } from "../../helper";

/* -------------------------------------------------------------------------------------------------
 * CarouselRoot
 * -----------------------------------------------------------------------------------------------*/

type Direction = null | "prev" | "next";
type CarouselStatus = {
  isAnimation: boolean;
  isPending: boolean;
};
type ApiObj = {
  count: number;
  current: number;
  dotCount: number;
};
type CarouselRootElement = React.ElementRef<"div">;
type CarouselRootProps = React.ComponentPropsWithoutRef<"div"> & {
  loop?: boolean;
  startPosition?: number;
  setApi?: null | React.Dispatch<React.SetStateAction<null | ApiObj>>;
  autoPlayDirection?: Direction;
  autoPlaySpeed?: number;
};
type CarouselRootContextValue = {
  childCount: number;
  setChildCount: React.Dispatch<React.SetStateAction<number>>;
  current: number;
  setCurrent: React.Dispatch<React.SetStateAction<number>>;
  carouselStatus: CarouselStatus;
  setCarouselStatus: React.Dispatch<React.SetStateAction<CarouselStatus>>;
  slidesView: number;
  setSlidesView: React.Dispatch<React.SetStateAction<number>>;
  duplicateLastRange: number;
  firstStartIndex: number;
  loopConditionHandler: () => void;
  accumulateDistanceHandler: (direction: Direction) => void;
  moveToSpecificLocationHandler: (newCurrent: number) => void;
  loop?: boolean;
  startPosition?: number;
};

const carouselRootBaseStyle = "relative";
const CarouselRootContext = createContext<CarouselRootContextValue>(null);
const defaultCarouselStatus = {
  isAnimation: false,
  isPending: false,
};

const useCarouselRootContext = () => {
  const carouselRootApi = useContext(CarouselRootContext);
  if (!carouselRootApi) {
    throw new Error(
      "carouselRootContext should be used in carouselRootContextProvider"
    );
  }
  return carouselRootApi;
};

const CarouselRoot = forwardRef<CarouselRootElement, CarouselRootProps>(
  (
    {
      children,
      className,
      loop = true,
      setApi = null,
      startPosition = 1,
      ...props
    },
    ref
  ) => {
    const [current, setCurrent] = useState<number | null>(null);
    const [childCount, setChildCount] = useState(0);
    const [slidesView, setSlidesView] = useState(0);

    const duplicateLastRange =
      childCount % slidesView === 0 ? slidesView : childCount % slidesView;
    const loopChildCount = duplicateLastRange + childCount + slidesView;

    // init current based on startPosition and duplicateLastRange
    const initCurrent =
      slidesView * (startPosition - 1) + (loop ? duplicateLastRange : 0);
    useEffect(() => {
      setCurrent(initCurrent);
    }, [initCurrent]);

    // moveInfo
    const firstStartIndex = loop ? duplicateLastRange + 0 : 0;
    const lastStartIndex = loop ? childCount : childCount - 1 - slidesView + 1;

    const [carouselStatus, setCarouselStatus] = useState<CarouselStatus>(
      defaultCarouselStatus
    );

    const moveDistanceHandler = (newCurrent: number) => {
      setCarouselStatus({ isAnimation: true, isPending: true });
      setCurrent(newCurrent);
    };

    const loopConditionHandler = () => {
      if (!loop) return;

      const fakeFirstStartIndex = 0;
      const fakeLastStartIndex = loopChildCount - 1 - slidesView + 1;
      let realPosition: number;

      if (current >= fakeLastStartIndex) {
        realPosition = firstStartIndex;
      }

      if (current <= fakeFirstStartIndex) {
        realPosition = lastStartIndex;
      }

      if (!realPosition) return;

      setCurrent(realPosition);
      setCarouselStatus({ isAnimation: false, isPending: false });
    };

    const accumulateDistanceHandler = (direction: Direction = "next") => {
      if (carouselStatus.isPending) return;

      const prevStartIndex = current;
      const prevEndIndex = current + slidesView - 1;
      const lastIndex = loop ? loopChildCount - 1 : childCount - 1;
      const moveInfoObj = {
        prev:
          prevStartIndex - slidesView <= 0 ? 0 : prevStartIndex - slidesView,
        next:
          prevEndIndex + slidesView >= lastIndex
            ? prevStartIndex + (lastIndex - prevEndIndex)
            : prevStartIndex + slidesView,
      };
      const newCurrent = moveInfoObj[direction];
      const notLoopCondition =
        (newCurrent <= firstStartIndex && prevStartIndex <= firstStartIndex) ||
        (newCurrent >= lastStartIndex && prevStartIndex >= lastStartIndex);

      if (!loop && notLoopCondition) {
        return;
      }

      moveDistanceHandler(newCurrent);
    };

    const moveToSpecificLocationHandler = (moveCount: number) => {
      if (carouselStatus.isPending) return;

      let newCurrent: number;
      if (moveCount === 0) {
        newCurrent = firstStartIndex;
      } else {
        newCurrent = firstStartIndex + moveCount * slidesView;
      }
      if (newCurrent === current) return;
      moveDistanceHandler(newCurrent);
    };

    const contextValue = useMemo(
      () => ({
        current,
        setCurrent,
        childCount,
        setChildCount,
        carouselStatus,
        setCarouselStatus,
        slidesView,
        setSlidesView,
        firstStartIndex,
        duplicateLastRange,
        loop,
        startPosition,
        loopConditionHandler,
        accumulateDistanceHandler,
        moveToSpecificLocationHandler,
      }),
      [
        current,
        childCount,
        carouselStatus,
        loop,
        startPosition,
        slidesView,
        firstStartIndex,
        duplicateLastRange,
        loopConditionHandler,
        accumulateDistanceHandler,
        moveToSpecificLocationHandler,
      ]
    );

    // expose carousel api object
    useEffect(() => {
      if (setApi === null) return;

      if (childCount <= 0 || slidesView <= 0) return;
      const apiObj = {
        count: childCount,
        current,
        dotCount: Math.ceil(childCount / slidesView),
      };
      setApi(apiObj);
    }, [current, slidesView, childCount]);

    return (
      <CarouselRootContext.Provider value={contextValue}>
        <div
          className={cn(carouselRootBaseStyle, className)}
          ref={ref}
          {...props}
        >
          {children}
        </div>
      </CarouselRootContext.Provider>
    );
  }
);

/* -------------------------------------------------------------------------------------------------
 * CarouselGroup
 * -----------------------------------------------------------------------------------------------*/

type CarouselGroupElement = React.ElementRef<"div">;
type CarouselGroupProps = React.ComponentPropsWithoutRef<"div"> & {
  asChild?: boolean;
};
const carouselGroupBaseStyle = "relative w-full h-full rounded overflow-hidden";
const CarouselGroup = forwardRef<CarouselGroupElement, CarouselGroupProps>(
  ({ asChild = false, children, className, ...props }, ref) => {
    const Comp = asChild ? Slot : "div";
    return (
      <Comp
        className={cn(carouselGroupBaseStyle, className)}
        ref={ref}
        {...props}
      >
        {children}
      </Comp>
    );
  }
);

/* -------------------------------------------------------------------------------------------------
 * CarouselOverlay
 * -----------------------------------------------------------------------------------------------*/

type CarouselOverlayElement = React.ElementRef<"div">;
type CarouselOverlayProps = React.ComponentPropsWithoutRef<"div"> & {
  asChild?: boolean;
};
const carouselOverlayBaseStyle =
  "pointer-events-none absolute top-0 w-full h-full flex justify-between items-center ";
const CarouselOverlay = forwardRef<
  CarouselOverlayElement,
  CarouselOverlayProps
>(({ asChild = false, children, className, ...props }, ref) => {
  const Comp = asChild ? Slot : "div";
  return (
    <Comp
      className={cn(carouselOverlayBaseStyle, className)}
      ref={ref}
      {...props}
    >
      {children}
    </Comp>
  );
});

/* -------------------------------------------------------------------------------------------------
 * CarouselTrigger
 * -----------------------------------------------------------------------------------------------*/

type CarouselTriggerElement = React.ElementRef<"div">;
type CarouselTriggerProps = React.ComponentPropsWithoutRef<"div"> & {
  asChild?: boolean;
  direction?: Direction;
};
const carouselTriggerBaseStyle = "p-2 pointer-events-auto cursor-pointer z-10";
// const carouselTriggerDisabledStyle = "cursor-default";
const CarouselTrigger = forwardRef<
  CarouselTriggerElement,
  CarouselTriggerProps
>(
  (
    { asChild = false, children, className, direction = "next", ...props },
    ref
  ) => {
    const { accumulateDistanceHandler } = useCarouselRootContext();
    const clickHandler = () => {
      accumulateDistanceHandler(direction);
    };

    // const disabledStyle = carouselTriggerDisabledStyle;

    const Comp = asChild ? Slot : "div";
    return (
      <Comp
        className={cn(carouselTriggerBaseStyle, className)}
        ref={ref}
        onClick={clickHandler}
        {...props}
      >
        {children}
      </Comp>
    );
  }
);

/* -------------------------------------------------------------------------------------------------
 * CarouselContent
 * -----------------------------------------------------------------------------------------------*/

type CarouselContentElement = React.ElementRef<"ul">;
type CarouselContentProps = React.ComponentPropsWithoutRef<"ul"> & {
  children: React.ReactNode[];
  asChild?: boolean;
};
type CarouselContentValue = {
  listWidth: number;
  setListWidth: React.Dispatch<React.SetStateAction<number>>;
};

const CarouselContentContext = createContext<CarouselContentValue>(null);
const carouselContentBaseStyle = "h-full flex";
const useCarouselContentContext = () => {
  const carouselContentApi = useContext(CarouselContentContext);
  if (!carouselContentApi) {
    throw new Error(
      "carouselContent should be used in carouselContentProvider"
    );
  }
  return carouselContentApi;
};
const CarouselContent = forwardRef<
  CarouselContentElement,
  CarouselContentProps
>(({ asChild = false, children, className, ...props }, ref) => {
  const {
    current,
    setChildCount,
    carouselStatus,
    setCarouselStatus,
    slidesView,
    setSlidesView,
    loop,
    duplicateLastRange,
    loopConditionHandler,
    accumulateDistanceHandler,
  } = useCarouselRootContext();

  // get child count, child + duplicate first child and last child
  const childCount = children.length;
  const slides: React.ReactNode[] | React.ReactNode = useMemo(() => {
    if (childCount === 1) {
      return children[0];
    }

    if (!loop) {
      return children;
    }

    const duplicateFirstGroup = (
      children.slice(0, slidesView) as React.ReactNode[]
    ).map((item, index) => {
      const key = 0 - index;
      return <Fragment key={key}>{item}</Fragment>;
    });

    const duplicateLastGroup = (
      children.slice(-duplicateLastRange) as React.ReactNode[]
    ).map((item, index) => {
      const key = childCount + index;
      return <Fragment key={key}>{item}</Fragment>;
    });

    return [...duplicateLastGroup, ...children, ...duplicateFirstGroup];
  }, [loop, children, childCount, slidesView]);

  // update childCount
  useEffect(() => {
    setChildCount(childCount);
  }, [setChildCount, childCount]);

  // set list width,create context for list
  const [listWidth, setListWidth] = useState(0);
  const contextValue = useMemo(
    () => ({
      listWidth,
      setListWidth,
    }),
    [listWidth]
  );
  // set slideView
  const carouselContentRef = useRef<HTMLUListElement>(null);
  useImperativeHandle(ref, () => carouselContentRef.current);
  useEffect(() => {
    const ContentEle = carouselContentRef.current;
    if (!ContentEle) return;
    const resizeObserver = new ResizeObserver((entries) => {
      const { contentRect } = entries[0];
      if (!contentRect.right || !listWidth) return;
      const newSlidesView = Math.round(contentRect.right / listWidth);
      setSlidesView(newSlidesView);
    });

    resizeObserver.observe(ContentEle);

    return () => {
      if (!ContentEle) return;
      resizeObserver.unobserve(ContentEle);
    };
  }, [listWidth]);

  const [move, setMove] = useState<null | number>(null);
  const isDefaultMove = move === null;
  const isMove = move !== 0;
  const transformX = listWidth > 0 && -(listWidth * current - move);

  const pointerDownHandler = () => {
    if (carouselStatus.isPending) return;
    setMove(0);
  };
  const pointerMoveHandler = (e: React.PointerEvent) => {
    if (e.pressure === 0) return;
    if (isDefaultMove) return;
    setCarouselStatus((prev) => ({
      ...prev,
      isAnimation: false,
    }));

    setMove((prev) => prev + e.movementX);
  };
  const pointerEndHandler = () => {
    if (isDefaultMove || !isMove) return;

    let direction: Direction = "next";
    if (move > 0) {
      direction = "prev";
    }
    accumulateDistanceHandler(direction);
    setMove(null);
  };

  const transitionEndHandler = () => {
    loopConditionHandler();
    setCarouselStatus((prev) => ({
      ...prev,
      isPending: false,
    }));
  };

  const Comp = asChild ? Slot : "ul";

  // 觸摸開始位置
  const [touchStartX, setTouchStartX] = useState<number>(0);
  // 移動距離
  const [moveX, setMoveX] = useState<number>(0);

  const touchStartHandler = (e: React.TouchEvent) => {
    if (carouselStatus.isPending) return;
    setTouchStartX(e.touches[0].clientX);
  };

  const touchMoveHandler = (e: React.TouchEvent) => {
    if (carouselStatus.isPending) return;
    // 計算移動距離
    const currentTouchX = e.touches[0].clientX;
    setMoveX(currentTouchX - touchStartX);
  };

  const touchEndHandler = () => {
    const direction: Direction = moveX > 0 ? 'prev' : 'next';
    // 根據滑動距離判斷觸發方向
    if (Math.abs(moveX) > 50) { // 假設滑動距離超過50px則切換
      accumulateDistanceHandler(direction);
    }
    setMoveX(0); // 重置移動距離
  };

  return (
    <CarouselContentContext.Provider value={contextValue}>
      <Comp
        className={cn(carouselContentBaseStyle, className)}
        style={{
          transform: `translate3d(${transformX}px,0,0)`,
          transition: carouselStatus.isAnimation ? "600ms" : "0ms",
        }}
        onTransitionEnd={transitionEndHandler}
        onPointerDown={pointerDownHandler}
        onPointerMove={pointerMoveHandler}
        onPointerUp={pointerEndHandler}
        onPointerLeave={pointerEndHandler}
        onTouchStart={touchStartHandler}
        onTouchMove={touchMoveHandler}
        onTouchEnd={touchEndHandler}
        ref={carouselContentRef}
        {...props}
      >
        {slides}
      </Comp>
    </CarouselContentContext.Provider>
  );
});

/* -------------------------------------------------------------------------------------------------
 * CarouselItem
 * -----------------------------------------------------------------------------------------------*/

type CarouselItemElement = React.ElementRef<"li">;
type CarouselItemProps = React.ComponentPropsWithoutRef<"li"> & {
  asChild?: boolean;
};
const CarouselItemBaseStyle = "w-full min-h-full shrink-0 ";
const CarouselItem = forwardRef<CarouselItemElement, CarouselItemProps>(
  ({ asChild = false, children, className, ...props }, ref) => {
    const { setCarouselStatus } = useCarouselRootContext();
    const { setListWidth } = useCarouselContentContext();
    const carouseItemRef = useRef<HTMLLIElement>(null);
    useImperativeHandle(ref, () => carouseItemRef.current);
    useEffect(() => {
      const ItemEle = carouseItemRef.current;
      if (!ItemEle) return;
      const resizeObserver = new ResizeObserver((entries) => {
        const { contentRect } = entries[0];
        setListWidth(contentRect?.right);
        setCarouselStatus({ isAnimation: false, isPending: false });
      });

      resizeObserver.observe(ItemEle);

      return () => {
        if (!ItemEle) return;
        resizeObserver.unobserve(ItemEle);
      };
    }, [setCarouselStatus]);

    const Comp = asChild ? Slot : "li";
    return (
      <Comp
        className={cn(CarouselItemBaseStyle, className)}
        ref={carouseItemRef}
        {...props}
      >
        {children}
      </Comp>
    );
  }
);

/* -------------------------------------------------------------------------------------------------
 * CarouselNav
 * -----------------------------------------------------------------------------------------------*/

type CarouselNavObj = {
  variant?: "picture" | "dot";
};
type CarouselNavElement = React.ElementRef<"ul">;
type CarouselNavProps = React.ComponentPropsWithoutRef<"ul"> & CarouselNavObj;
type CarouselNavContextValue = CarouselNavObj;

const CarouselNavBaseStyle = "ml-[-5px]  w-full flex items-center";
const CarouselNavPictureBaseStyle = `${CarouselNavBaseStyle} mt-2 overflow-x-auto  justify-between`;
const CarouselNavDotBaseStyle = `${CarouselNavBaseStyle} absolute bottom-2  justify-center`;
const CarouselNavContext = createContext<CarouselNavContextValue>(null);

const useCarouselNavContext = () => {
  const carouselNavApi = useContext(CarouselNavContext);
  if (!carouselNavApi) {
    throw new Error(
      "carouselNavContext should be used in carouselNavContextProvider"
    );
  }
  return carouselNavApi;
};
const CarouselNav = forwardRef<CarouselNavElement, CarouselNavProps>(
  ({ children, className, variant = "picture", ...props }, ref) => {
    const navStyleObj = {
      picture: CarouselNavPictureBaseStyle,
      dot: CarouselNavDotBaseStyle,
    };

    const contextValue = useMemo(
      () => ({
        variant,
      }),
      [variant]
    );
    return (
      <CarouselNavContext.Provider value={contextValue}>
        <ul
          className={cn(navStyleObj[variant], className)}
          ref={ref}
          {...props}
        >
          {children}
        </ul>
      </CarouselNavContext.Provider>
    );
  }
);

/* -------------------------------------------------------------------------------------------------
 * CarouselNavItem
 * -----------------------------------------------------------------------------------------------*/

type CarouselNavItemElement = React.ElementRef<"li">;
type CarouselNavItemProps = React.ComponentPropsWithoutRef<"li"> & {
  index: number;
  customActiveStyle?: string;
};

const CarouselNavItemBaseStyle = "ml-1 cursor-pointer";
const CarouselNavItemPictureBaseStyle = `${CarouselNavItemBaseStyle} rounded overflow-hidden shrink-0 h-full`;
const CarouselNavItemActiveBaseStyle = "border border-red-600 border-2";

const CarouselNavItemDotBaseStyle = `${CarouselNavItemBaseStyle} rounded-full w-2 h-2 bg-white-500`;
const CarouselNavItemActiveDotBaseStyle = "bg-red-500";
const CarouselNavItem = forwardRef<
  CarouselNavItemElement,
  CarouselNavItemProps
>(({ children, className, customActiveStyle = "", index, ...props }, ref) => {
  const {
    current,
    loop,
    firstStartIndex,
    slidesView,
    moveToSpecificLocationHandler,
  } = useCarouselRootContext();

  const clickHandler = () => {
    moveToSpecificLocationHandler(index);
  };

  const notLoopCondition = current === index;
  const loopCondition = current === firstStartIndex + index * slidesView;
  const isActive = loop ? loopCondition : notLoopCondition;

  // navItem css
  const navItemStyleObj = {
    picture: {
      style: CarouselNavItemPictureBaseStyle,
      active: CarouselNavItemActiveBaseStyle,
      customActiveStyle,
    },
    dot: {
      style: CarouselNavItemDotBaseStyle,
      active: CarouselNavItemActiveDotBaseStyle,
      customActiveStyle,
    },
  };

  const { variant } = useCarouselNavContext();
  const activeStyle =
    isActive && (customActiveStyle || navItemStyleObj[variant].active);

  // for pictureNav
  const carouselNavItemRef = useRef<HTMLLIElement>(null);
  useImperativeHandle(ref, () => carouselNavItemRef.current);
  const isPictureType = variant === "picture";
  useEffect(() => {
    if (!isPictureType) return;
    if (!carouselNavItemRef.current) return;
    if (!isActive) return;

    carouselNavItemRef.current.scrollIntoView({
      behavior: "smooth",
      block: "nearest",
      inline: "center",
    });
  }, [isPictureType, isActive]);

  return (
    <li
      className={cn(navItemStyleObj[variant].style, className, activeStyle)}
      ref={carouselNavItemRef}
      {...props}
    >
      <button
        className={cn("block h-full w-full")}
        type="button"
        onClick={clickHandler}
      >
        {children}
      </button>
    </li>
  );
});

const Carousel = {
  Root: CarouselRoot,
  Group: CarouselGroup,
  Overlay: CarouselOverlay,
  Trigger: CarouselTrigger,
  Content: CarouselContent,
  Item: CarouselItem,
  Nav: CarouselNav,
  NavItem: CarouselNavItem,
};

export {
  Carousel,
  CarouselRoot,
  CarouselGroup,
  CarouselOverlay,
  CarouselTrigger,
  CarouselContent,
  CarouselItem,
  CarouselNav,
  CarouselNavItem,
  type ApiObj,
};
