import { forwardRef, useState } from "react";
import { Slot } from "@radix-ui/react-slot";
import { Button } from "../button";

// Pagination Root
type PaginationRootElement = React.ElementRef<"div">;
type PaginationRootProps = React.ComponentPropsWithoutRef<"div"> & {
    asChild?: boolean;
};
const PaginationRoot = forwardRef<PaginationRootElement, PaginationRootProps>(
    ({ asChild = false, children, className, ...props }, ref) => {
        const Comp = asChild ? Slot : "div";

        return (
            <Comp className={className} ref={ref} {...props}>
                {children}
            </Comp>
        );
    }
);

// Pagination Content
type PaginationContentElement = React.ElementRef<"div">;
type PaginationContentProps = React.ComponentPropsWithoutRef<"div"> & {
    asChild?: boolean;
};
const PaginationContent = forwardRef<
    PaginationContentElement,
    PaginationContentProps
>(({ asChild = false, children, className, ...props }, ref) => {
    const Comp = asChild ? Slot : "div";
    return (
        <Comp className={className} ref={ref} {...props}>
            {children}
        </Comp>
    );
});

// Pagination Item
type PaginationItemElement = React.ElementRef<"button">;
type PaginationItemProps = React.ComponentPropsWithoutRef<"button"> & {
    asChild?: boolean;
};
const PaginationItem = forwardRef<PaginationItemElement, PaginationItemProps>(
    ({ children, className, ...props }, ref) => (
        <Button className={className} ref={ref} {...props}>
            {children}
        </Button>
    )
);

// Pagination Previous
type PaginationPreviousElement = React.ElementRef<"div">;
type PaginationPreviousProps = React.ComponentPropsWithoutRef<"div"> & {
    asChild?: boolean;
    href?: string;
};
const PaginationPrevious = forwardRef<
    PaginationPreviousElement,
    PaginationPreviousProps
>(({ href, children, className, ...props }, ref) => (
    <div className={className} ref={ref} {...props}>
        {children || (
            <svg
                width="16"
                height="16"
                viewBox="0 0 16 16"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
            >
                <path
                    d="M10.42 11.58L9.57162 12.4287L5.57031 8.42867C5.33591 8.19434 5.33591 7.81433 5.57031 7.58L9.57162 3.58L10.42 4.42867L6.84317 8.00434L10.42 11.58Z"
                    fill="currentColor"
                />
            </svg>
        )}
    </div>
));

// Pagination Next
type PaginationNextElement = React.ElementRef<"div">;
type PaginationNextProps = React.ComponentPropsWithoutRef<"div"> & {
    asChild?: boolean;
    href?: string;
};
const PaginationNext = forwardRef<PaginationNextElement, PaginationNextProps>(
    ({ href, children, className, ...props }, ref) => (
        <div className={className} ref={ref} {...props}>
            {children || (
                <svg
                    width="16"
                    height="16"
                    viewBox="0 0 16 16"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <path
                        d="M5.57999 4.42L6.42838 3.57133L10.4297 7.57133C10.6641 7.80566 10.6641 8.18567 10.4297 8.42L6.42838 12.42L5.57999 11.5713L9.15683 7.99566L5.57999 4.42Z"
                        fill="currentColor"
                    />
                </svg>
            )}
        </div>
    )
);

// Pagination Ellipsis
type PaginationEllipsisElement = React.ElementRef<"button">;
type PaginationEllipsisProps = React.ComponentPropsWithoutRef<"button">;

const PaginationEllipsis = forwardRef<
    PaginationEllipsisElement,
    PaginationEllipsisProps
>(({ className, children, onClick, ...props }, ref) => {
    const [isHovered, setIsHovered] = useState(false);

    const handleMouseEnter = () => {
        setIsHovered(true);
    };

    const handleMouseLeave = () => {
        setIsHovered(false);
    };

    return (
        <button
            type="button"
            className={className}
            ref={ref}
            {...props}
            onClick={onClick}
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
        >
            {children || (
                <svg
                    width="40"
                    height="35"
                    viewBox="0 0 40 35"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <rect width="40" height="35" rx="3" fill="none" />
                    <circle cx="12" cy="17" r="2" fill={isHovered ? "#FFFFFF" : "#222222"} />
                    <circle cx="20" cy="17" r="2" fill={isHovered ? "#FFFFFF" : "#222222"} />
                    <circle cx="28" cy="17" r="2" fill={isHovered ? "#FFFFFF" : "#222222"} />
                </svg>
            )}
        </button>
    );
});

const Pagination = {
    Root: PaginationRoot,
    Content: PaginationContent,
    Item: PaginationItem,
    Previous: PaginationPrevious,
    Next: PaginationNext,
    Ellipsis: PaginationEllipsis,
};

export {
    Pagination,
    PaginationRoot,
    PaginationContent,
    PaginationItem,
    PaginationPrevious,
    PaginationNext,
    PaginationEllipsis,
};

interface CalcPaginationItemProps {
    index: number;
    currentPage: number;
    totalPage: number;
    handlePageChange: (page: number) => void;
    className?: string;
}

export function CalcPaginationItem({
    index,
    currentPage,
    totalPage,
    handlePageChange,
    className,
}: CalcPaginationItemProps) {
    let pageNum = index;
    let isEllipsis = false;

    if (index > totalPage) return null;

    // Calculate page number or ellipsis for each grid based on current page and total pages
    if (totalPage > 9) {
        if (index === 2) {
            isEllipsis = currentPage >= 6;
            pageNum = isEllipsis ? currentPage - 5 : pageNum;
        } else if (index === 8) {
            isEllipsis = currentPage <= totalPage - 5;
            pageNum = isEllipsis ? currentPage + 5 : totalPage - 1;
        } else if (index === 9) {
            pageNum = totalPage;
        } else if (index > 2 && index < 8) {
            if (currentPage < 6) {
                pageNum = index;
            } else if (currentPage > totalPage - 5) {
                pageNum = totalPage - 9 + index;
            } else {
                pageNum = currentPage - 5 + index;
            }
        }
    }

    if (isEllipsis) {
        return (
            <Pagination.Ellipsis
                key={index}
                onClick={() => handlePageChange(pageNum)}
                className={className}
            />
        );
    }
    return (
        <Pagination.Item
            key={index}
            onClick={() => handlePageChange(pageNum)}
            className={className}
        >
            {pageNum}
        </Pagination.Item>
    );
}
