type Direction = "Right" | "Left" | "Top" | "Bottom";
export type Keyframes = { [key: string]: { [key: string]: { [property: string]: string } } };
export type Animations = { [key: string]: string };

export const generateSlideAnimations = (directions: Direction[], duration: number = 300): { keyframes: Keyframes; animation: Animations } => {
    const keyframes: Keyframes = {};
    const animation: Animations = {};

    function capitalizeFirstLetter(dir: string): string {
        return dir.toLowerCase().replace(/^(.)/, (match) => match.toUpperCase());
    }

    directions.forEach((dir) => {
        const axis = dir === "Right" || dir === "Left" ? "X" : "Y";
        const distance = dir === "Right" || dir === "Bottom" ? "100%" : "-100%";

        const slideInKey = `slideIn${dir}`;
        const slideOutKey = `slideOut${dir}`;
        const inAnimationName = `slideIn${capitalizeFirstLetter(dir)}${duration}`;
        const outAnimationName = `slideOut${capitalizeFirstLetter(dir)}${duration}`;

        keyframes[slideInKey] = {
            '0%': { transform: `translate${axis}(${distance})` },
            '100%': { transform: `translate${axis}(0)` },
        };
        keyframes[slideOutKey] = {
            '0%': { transform: `translate${axis}(0)` },
            '100%': { transform: `translate${axis}(${distance})` },
        };

        animation[inAnimationName] = `${slideInKey} ${duration}ms ease-in-out forwards`;
        animation[outAnimationName] = `${slideOutKey} ${duration}ms ease-in-out forwards`;
    });

    return { keyframes, animation };
};

export const slideAnimations = {};
for (let i = 1; i <= 10; i += 1) {
    const duration = i * 100; // 從100毫秒開始，每次增加100毫秒
    slideAnimations[`slideAnimations${i}`] = generateSlideAnimations(["Right", "Left", "Top", "Bottom"], duration);
}
