import plugin from "tailwindcss/plugin";
import { coverTheme, extendTheme } from "../styles";

const lionColor = {
  colors: {
    red: {
      400: "#F35963",
      500: "#F03742",
      600: "#BB0400",
    },
    black: {
      500: "#404644",
    },
    blue: {
      400: "#6ABAE2",
      500: "#3EA5D9",
      600: "#268CC0 ",
    },
    orange: {
      400: "#FF9166",
      500: "#FF7238",
      600: "#FF5B1A ",
    },
    slate: {
      200: "#999999",
      300: "#666666",
      400: "#444444",
      500: "#222222",
    },
    stone: {
      200: "#EAEBF1",
      300: "#DDDDDD",
      400: "#BBBBBB",
      500: "#7D8282",
    },
    neutral: {
      200: "#EAEBF1",
      300: "#F5F5F5",
      400: "#F1F1F1",
      500: "#EBEBEB",
    },
    white: {
      500: "#ffffff",
    },
    transparent: "transparent",
  },
};

const lionFontStyle = {
  ".h1-regular": {
    fontSize: "30px",
    lineHeight: "38px",
    fontWeight: "400",
  },
  ".h1-bold": {
    fontSize: "30px",
    lineHeight: "38px",
    fontWeight: "700",
  },
  ".h2-regular": {
    fontSize: "24px",
    lineHeight: "32px",
    fontWeight: "400",
  },
  ".h2-bold": {
    fontSize: "24px",
    lineHeight: "32px",
    fontWeight: "700",
  },
  ".h3-regular": {
    fontSize: "20px",
    lineHeight: "30px",
    fontWeight: "400",
  },
  ".h3-bold": {
    fontSize: "20px",
    lineHeight: "30px",
    fontWeight: "700",
  },
  ".h4-regular": {
    fontSize: "18px",
    lineHeight: "28px",
    fontWeight: "400",
  },
  ".h4-bold": {
    fontSize: "18px",
    lineHeight: "28px",
    fontWeight: "700",
  },
  ".h5-regular": {
    fontSize: "16px",
    lineHeight: "24px",
    fontWeight: "400",
  },
  ".h5-bold": {
    fontSize: "16px",
    lineHeight: "24px",
    fontWeight: "700",
  },
  ".body-regular": {
    fontSize: "14px",
    lineHeight: "22px",
    fontWeight: "400",
  },
  ".body-bold": {
    fontSize: "14px",
    lineHeight: "22px",
    fontWeight: "700",
  },
  ".small-regular": {
    fontSize: "12px",
    lineHeight: "22px",
    fontWeight: "400",
  },
  ".small-bold": {
    fontSize: "12px",
    lineHeight: "22px",
    fontWeight: "700",
  },
  ".subtle-regular": {
    fontSize: "10px",
    lineHeight: "22px",
    fontWeight: "400",
  },
  ".subtle-bold": {
    fontSize: "10px",
    lineHeight: "22px",
    fontWeight: "700",
  },
};

const lionPlugin = plugin(
  ({ matchUtilities, addBase, addUtilities, addComponents, theme }) => {
    matchUtilities(
      {
        "animation-delay": (value: string) => ({
          "animation-delay": value,
        }),
      },
      {
        values: theme("transitionDelay"),
      }
    );
    addBase({
      body: {
        fontFamily:
          "Noto Sans TC, Microsoft JhengHeim, Google sans-serif !important",
      },
      "*::-webkit-scrollbar": {
        width: "5px",
        height: "5px",
      },

      "*::-webkit-scrollbar-track": {
        background: "transparent",
        borderRadius: "5px",
      },

      "*::-webkit-scrollbar-thumb": {
        backgroundColor: " #ccc",
        borderRadius: "5px",
      },

      ".input-range::-ms-thumb":{
        pointerEvents: "all",
        appearance: "none",
        backgroundColor: 'red',
      },

      ".input-range::-moz-range-thumb":{
        appearance: "none",
        pointerEvents: "all",
        cursor: "grab",
        backgroundColor: 'red',
      },
      ".input-range::-webkit-slider-thumb":{
        appearance: 'none',
        pointerEvents: 'all',
        cursor: "grab",
        backgroundColor: 'red',
      }
    });
    addUtilities({
      ".overflow-unset": {
        overflow: "unset",
      },
      /* Hide scrollbar for Chrome, Safari and Opera */
      ".scroll-hidden::-webkit-scrollbar": {
        display: "none",
      },
      /* Hide scrollbar for IE, Edge and Firefox */
      ".scroll-hidden": {
        "-ms-overflow-style": "none" /* IE and Edge */,
        "scrollbar-width": "none" /* Firefox */,
      },
    });
    addComponents({ ...lionFontStyle });
  },
  {
    theme: {
      ...coverTheme,
      ...lionColor,
      extend: {
        ...extendTheme,
      },
    },
  }
);

export default lionPlugin;
